        content: `「金庸作品集」台灣版序
小說是寫給人看的。小說的內容是人。
小說寫一個人、幾個人、一羣人、或成千成萬人的性格和感情。他們的性格和感情從橫面的環境中反映出來，從縱面的遭遇中反映出來，從人與人之間的交往與關係中反映出來。長篇小說中似乎只有「魯濱遜飄流記」，才只寫一個人，寫他與自然之間的關係，但寫到後來，終於也出現了一個僕人「星期五」。只寫一個人的短篇小說多些，寫一個人在與環境的接觸中表現他外在的世界，內心的世界，尤其是內心世界。
西洋傳統的小說理論分別從環境、人物、情節三個方面去分析一篇作品。由於小說作者不同的個性與才能，往往有不同的偏重。
基本上，武俠小說與別的小說一樣，也是寫人，只不過環境是古代的，人物是有武功的，情節偏重於激烈的鬪爭。任何小說都有它所特別側重的一面。愛情小說寫男女之間與性有關的感情，寫實小說描繪一個特定時代的環境，「三國演義」與「水滸」一類小說敍述大羣人物的鬪爭經歷，現代小說的重點往往放在人物的心理過程上。
小說是藝術的一種，藝術的基本內容是人的感情，主要形式是美，廣義的、美學上的美。在小說，那是語言文筆之美、安排結構之美，關鍵在於怎樣將人物的內心世界通過某種形式而表現出來。甚麼形式都可以，或者是作者主觀的剖析，或者是客觀的敍述故事，從人物的行動和言語中客觀的表達。
讀者閱讀一部小說，是將小說的內容與自己的心理狀態結合起來。同樣一部小說，有的人感到強烈的震動，有的人卻覺得無聊厭倦。讀者的個性與感情，與小說中所表現的個性與感情相接觸，產生了「化學反應」。
武俠小說只是表現人情的一種特定形式。好像作曲家要表現一種情緒，用鋼琴、小提琴、交響樂、或歌唱的形式都可以，畫家可以選擇油畫、水彩、水墨、或漫畫的形式。問題不在採取甚麼形式，而是表現的手法好不好，能不能和讀者、聽者、觀賞者的心靈相溝通，能不能使他的心產生共鳴。小說是藝術形式之一，有好的藝術，也有不好的藝術。
好或者不好，在藝術上是屬於美的範疇，不屬於真或善的範疇。判斷美的標準是美，是感情，不是科學上的真或不真，道德上的善或不善，也不是經濟上的值錢不值錢，政治上對統治者的有利或有害。當然，任何藝術作品都會發生社會影響，自也可以用社會影響的價值去估量，不過那是另一種評價。
在中世紀的歐洲，基督教的勢力及於一切，所以我們到歐美的博物院去參觀，見到所有中世紀的繪畫都以聖經為題材，表現女性的人體之美，也必須通過聖母的形象。直到文藝復興之後，凡人的形象才在繪畫和文學中表現出來，所謂文藝復興，是在文藝上復興希臘、羅馬時代對「人」的描寫，而不再集中於描寫神與聖人。
中國人的文藝觀，長期來是「文以載道」，那和中世紀歐洲黑暗時代的文藝思想是一致的，用「善或不善」的標準來衡量文藝。「詩經」中的情歌，要牽強附會地解釋為諷刺君主或歌頌后妃。陶淵明的「閒情賦」，司馬光、歐陽修、晏殊的相思愛戀之詞，或者惋惜地評之為白璧之玷，或者好意地解釋為另有所指。他們不相信文藝所表現的是感情，認為文字的唯一功能只是為政治或社會價值服務。
我寫武俠小說，只是塑造一些人物，描寫他們在特定的武俠環境（古代的、沒有法治的、以武力來解決爭端的社會）中的遭遇。當時的社會和現代社會已大不相同，人的性格和感情卻沒有多大變化。古代人的悲歡離合、喜怒哀樂，仍能在現代讀者的心靈中引起相應的情緒。讀者們當然可以覺得表現的手法拙劣，技巧不夠成熟，描寫殊不深刻，以美學觀點來看是低級的藝術作品。無論如何，我不想載甚麼道。我在寫武俠小說的同時，也寫政治評論，也寫與哲學、宗教有關的文字。涉及思想的文字，是訴諸讀者理智的，對這些文字，才有是非、真假的判斷，讀者或許同意，或許只部份同意，或許完全反對。
對於小說，我希望讀者們只說喜歡或不喜歡，只說受到感動或覺得厭煩。我最高興的是讀者喜愛或憎恨我小說中的某些人物，如果有了那種感情，表示我小說中的人物已和讀者的心靈發生聯繫了。小說作者最大的企求，莫過於創造一些人物，使得他們在讀者心中變成活生生的、有血有肉的人。藝術是創造，音樂創造美的聲音，繪畫創造美的視覺形象，小說是想創造人物。假使只求如實反映外在世界，那麼有了錄音機、照相機，何必再要音樂、繪畫？有了報紙、歷史書、記錄電視片、社會調查統計、醫生的病歷紀錄、黨部與警察局的人事檔案，何必再要小說？
一九八六．二．六 於香港

第一回 危邦行蜀道 亂世壞長城

大明成祖皇帝永樂六年八月乙未，西南海外浡泥國國王麻那惹加那乃，率同妃子、弟、妹、世子、及陪臣來朝，進貢龍腦、鶴頂、玳瑁、犀角、金銀寶器等諸般物事。成祖皇帝大悅，嘉勞良久，賜宴奉天門。
那浡泥國即今婆羅洲北部的婆羅乃，又稱文萊（浡泥、婆羅乃、文萊、以及英語Brunei均係同一地名之音譯），雖和中土相隔海程萬里，但向來仰慕中華。宋朝太平興國二年，其王向打（即蘇丹，中國史書上譯音為「向打」）曾遣使來朝，進貢龍腦、象牙、檀香等物，其後朝貢不絕。
麻那惹加那乃國王眼見天朝上國民豐物阜，文治教化、衣冠器具，無不令他歡喜讚歎，明帝又相待甚厚，竟然留戀不去。到該年十一月，一來年老，二來水土不服，患病不治。成祖深為悼惜，為之輟朝三日，賜葬南京安德門外（今南京中華門外聚寶山麓，有王墓遺址，俗呼馬回回墳），又命世子遐旺襲封浡泥國王，遣使者護送歸國，賞賜金銀、器皿、錦綺、紗羅等物。
遐旺王奏稱：小國後山，頗有神異，乞皇上賜封，表為一國之鎮。
成祖便封其山名為「長寧鎮國山」，親製碑文，並題詩一首，詩曰：
「炎海之墟，浡泥所處。煦仁漸義，有順無迕。慺慺賢王，惟化之慕。
導以象胥，遹來奔赴。同其婦子，兄弟陪臣。稽顙闕下，有言以陳。
謂君猶天，遣其休樂。一視同仁，匪偏厚薄。顧茲鮮德，弗種所云。
浪舶風檣，實勞懇勤。稽古遠臣，順來怒趦。以躬或難，矧曰家室？
王心亶誠，金石其堅。西南蕃長，疇與王賢？矗矗高山，以鎮王國。
鑱文以石，懋昭王德。王德克昭，王國攸寧。於斯萬年，仰我大明。」
成祖皇帝的御製詩文，便刻在浡泥國長寧鎮國山的一塊大石碑上。此後洪熙、嘉靖、正德年間，均有朝貢。中國人去到浡泥國的，有些還做了大官，被封為「那督」。
到得萬曆年間，浡泥國內忽起內亂，「明史．浡泥傳」載稱：「其王卒，無嗣。族人爭立，國中殺戮幾盡，乃立其女為王。漳州人張姓者，初為其國那督，華言尊官也，因亂出奔，女王立，迎還之。其女出入王宮，得心疾，妄言父有反謀。女主懼，遣人按問其家，那督自殺。國人為訟寃。女主悔，絞殺其女，授其子官。」
這位張那督的女兒為何神經錯亂，向女王誣告父親造反，以致釀成這個悲劇，想必另有曲折內情，史書並未詳載，後人不得而知。福建漳州張氏在浡泥國累世受封那督，頗有權勢，為國人所敬。
華人在彼邦經商務農，數亦不少，披荊斬棘，甚有功績，和當地土人相處融洽。費信「星槎勝覽」一書中記云：「渤泥國……其國之民崇佛像，好齋沐。凡見唐人至其國，甚有愛敬。有醉者，則扶歸家寢宿，以禮待之若故舊。」有詩為證，詩曰：
「渤泥滄海外，立國自何年？夏冷冬生熱，山盤地自偏。
積修崇佛教，扶醉待賓賢。取信通商舶，遺風事可傳。」
浡泥國那督張氏數傳後是為張信，膝下惟有一子。張信不忘故國，為兒子取名朝唐。
到張朝唐十二歲那一年，福建有一名士人屢試不第，棄儒經商，隨著鄉人來到浡泥國。這人不善經營，本錢蝕的乾乾淨淨，無顏回鄉，就此流落異邦。有人薦他去見張信，想要謀個生計。張信和他一談之下，心下大喜，便即聘為西賓，教兒子讀書。
張朝唐開蒙雖遲，卻是天資聰穎，十年之間，四書五經俱已熟習。那老師力勸張信遣子回中土應試，若能考得個秀才、舉人，有了中華的功名，回到浡泥來那可是大有光采。張信也盼兒子回鄉去觀光上國風物，於是重重酬謝了老師，打點金銀行李，再派僮兒張康跟隨，命張朝唐隨同老師回漳州原籍應試。
其時正是崇禎六年，逆奄魏忠賢雖已伏誅，但在天啟朝七年之間禍國殃民，殺害忠良，天下元氣大傷，兼之連年水旱成災，流寇四起。張朝唐等三人從廈門上岸，僱船西上漳州。不料只行出數十里，四鄉忽然大亂，一羣盜賊湧上船來，不由分說，便將那教書先生殺了。張朝唐主僕幸好識得水性，跳水逃命，才免了一刀之厄。
兩人在鄉間躲了三日，聽得四鄉饑民聚眾要攻漳州、廈門。這一來，只將張朝唐嚇得滿腔雄心，登化烏有，眼見危邦不可居，還是急速回家的為是。其時廈門已不能再去，主僕兩人一商量，決定從陸路西赴廣州，再乘海船出洋。兩人買了兩匹坐騎，膽戰心驚，沿路打聽，向廣東而去。
幸喜一路無事，經南靖、平和，來到三河壩，已是廣東省境，再過梅縣、水口，向西迤邐行來。張朝唐素聞廣東是富庶之地，但沿途所見，盡是饑民，心想中華地大物博，百姓人人生死繫於一線，浡泥只是海外小邦，男女老幼卻是安居樂業，無憂無慮，不由得大是嘆息，心想中國山川雄奇，眼見者百未得一，但如此朝不保夕，還是去浡泥椰子樹下唱歌睡覺安樂得多了。
這一日行經鴻圖嶂，山道崎嶇，天色漸晚，他心中焦急起來，催馬急奔。一口氣奔出十多里地，到了一個小市鎮上，主僕兩人大喜，想找個客店借宿，那知道市鎮上靜悄悄的一個人影也無。張康下馬，走到一家掛著「粵東客棧」招牌的客店之外，高聲叫道：「喂，店家，店家！」店房靠山，山谷響應，只聽見「喂，店家，店家」的回聲，店裏卻毫無動靜。正在這時，一陣北風吹來，獵獵作響，兩人都感毛骨悚然。
張朝唐拔出佩劍，闖進店去，只見院子內地下倒著兩具屍首，流了一大灘黑血，蒼蠅繞著屍首亂飛。腐臭撲鼻，看來死者已死去多日。張康一聲大叫，轉身逃出店去。
張朝唐四下一瞧，到處箱籠散亂，門窗殘破，似經盜匪洗刦。張康見主人不出來，一步一頓的又回進店去。張朝唐道：「到別處看看。」那知又去了三家店鋪，家家都是如此。有的女屍身子赤裸，顯是曾遭強暴而後被殺。一座市鎮之中，到處陰風慘慘，屍臭陣陣。兩人再也不敢停留，急忙上馬向西。
主僕兩人行了十幾里，天色全黑，又餓又怕，正狼狽間，張康忽道：「公子，你瞧！」張朝唐順著他手指看去，只見遠處有一點火光，喜道：「咱們借宿去。」
兩人離開大道，向著火光走去，越走道路越是窄小。張朝唐忽道：「倘若那是賊窟，豈不是自投死路？」張康嚇了一跳，道：「那麼別去吧。」張朝唐眼見四下烏雲欲合，頗有雨意，說道：「先悄悄過去瞧一瞧。」於是下了馬，把馬縛在路邊樹上，躡足向火光處走去。
行到臨近，見是兩間茅屋，張朝唐想到窗口往裏窺探，忽然一隻狗大聲吠叫，撲了過來。張朝唐揮動佩劍，那狗才不敢走近，只是亂叫。
柴扉開處，一個老婆婆走了出來，手中舉著一盞油燈，顫巍巍的詢問是誰。張朝唐道：「我們是過路客人，錯過了宿頭，想在府上借宿一晚。」老婆婆微一遲疑，道：「請進來吧。」張朝唐走進茅屋，見屋裏只有一張土床，桌椅俱無。床上躺著一個老頭，不斷咳嗽。張朝唐命張康去把馬牽來。張康想起剛才見到的死人慘狀，畏畏縮縮的不敢出去。那老頭兒挨下床來，陪著他去牽了馬來。老婆婆拿出幾個玉米餅來饗客，燒了一壺熱水給他們喝。
張朝唐吃了一個玉米餅，問道：「前面鎮上殺了不少人，是甚麼匪幫幹的？」老頭兒嘆了口氣，道：「甚麼匪幫？土匪有這麼狠嗎？那是官兵幹的好事。」張朝唐大吃一驚，道：「官兵？官兵怎麼會這樣無法無天、奸淫擄掠？他們長官不理嗎？」
老頭兒冷笑一聲，說道：「你這位小相公看來是第一次出門，甚麼世情也不懂的了。長官？長官帶頭幹呀，好的東西他先拿，好看的娘們他先要。」張朝唐道：「老百姓怎不向官府去告？」老頭兒道：「告有甚麼用？你一告，十之八九還陪上了自己性命。」張朝唐道：「那怎樣說？」老頭兒道：「那還不是官官相護？別說官老爺不會准你狀子，還把你一頓板子收了監。你沒錢孝敬，就別想出來啦。」
張朝唐不住搖頭，又問：「官兵到山裏來幹麼？」老頭兒道：「說是來剿匪殺賊，其實山裏的盜賊，十個中倒有八個是給官府逼得沒生路才幹的。官兵下鄉來捉不到強盜，擄掠一陣，再亂殺些老百姓，提了首級上去報功，發了財，還好升官。」那老頭兒說得咬牙切齒，又不停的咳嗽。老婆婆不住向他打手勢，叫他別說了，只怕張朝唐識得官家，多言惹禍。
張朝唐聽的悶悶不樂，想不到世局敗壞如此，心想：「爹爹常說，中華是文物禮義之邦，王道教化，路不拾遺，夜不閉戶，人人講信修睦，仁義和愛。今日眼見，卻是大不盡然，還遠不如浡泥國蠻夷之地。」感嘆了一會，就倒在床上睡了。
剛朦朧合眼，忽聽見門外犬吠之聲大作，跟著有人怒喝叫罵，蓬蓬蓬的猛力打門。老婆婆下床來要去開門，老頭兒搖手止住，輕輕對張朝唐道：「相公，你到後面躲一躲。」
張朝唐和張康走到屋後，聞到一陣新鮮的稻草氣息，想是堆積柴草的所在，只聽見格啦啦一陣響，屋門已被推倒，一人粗聲喝道：「幹麼不開門？」也不等回答，拍的一聲，有人給打了記耳光。
老婆婆道：「上差老爺，我……我們老夫妻年老胡塗，耳朵不好，沒聽見。」那知又是一記耳光，那人罵道：「沒聽見就該打。快殺雞，做四個人的飯。」老頭兒道：「我們人都快餓死啦，那裏有甚麼雞？」只聽蓬的一聲，似乎老頭兒被推倒在地，老婆婆哭叫起來。
又聽另一個聲音道：「老王，算了吧，今日跑了整整一天，只收到三兩七錢稅銀，大家心裏不痛快，你拿他出氣也沒用。」那老王道：「這種人，你不用強還行？這幾兩銀子，不是我打斷那鄉下佬的狗腿，這些土老兒們肯乖乖拿出來嗎？」另一個嘶啞的聲音道：「這些鄉下佬也真是的，窮的米缸裏數來數去也得十幾粒米，再逼實在也逼不出甚麼來啦，只是大老爺又得罵咱們兄弟沒用……」
正說話間，忽然張朝唐的馬嘶叫起來。幾名公差一驚，出門查看，見到兩匹馬，議論起來，說乘馬之人定在屋中借宿，看來倒有一筆油水，當即興興頭頭的進屋來尋。
張朝唐大驚，一扯張康的手，輕輕從後門溜了出去。兩人一腳高一腳低，在山裏亂走，見無人追來，才放了心，幸虧所帶的銀兩張康都揹在背上。
兩人在樹叢中躲了一宵，等天色大亮，才慢慢摸到大道上來。主僕兩人行出十多里，商量到前面市鎮再買代步腳力。張康不住痛罵公差害人。正罵得痛快，忽然斜刺小路裏走來四名公差，手中拿著鍊條鐵尺，後面兩人各牽著一匹馬，那正是他們的坐騎。
張朝唐和張康面面相覷，這時要避開已經來不及，只得裝作若無其事，繼續走路。
那四名公差不住向他們打量，一名滿臉橫肉的公差斜眼問道：「喂，朋友，幹甚麼的？」
張朝唐一聽口音，正是昨晚打人的那個老王。張康走上一步，道：「那是我們公子爺，要上廣州去讀書。」
老王一把揪住，挾手奪過他背上包裹，打開一看，見纍纍盡是黃金白銀，不由得驚喜交集，喝道：「甚麼公子爺？瞧你兩個都不是好東西！這些金銀那裏來的？定是偷來騙來的，好，現今拿到賊贓啦，跟我見大老爺去。」他見這兩人年幼好欺，想把他們嚇跑。
那知張康道：「我們公子爺是外國大官，知府大人見了他也客客氣氣。見你們老爺去，那是再好也沒有啦！」
一名中年公差聽了這話，眉頭一皺，心想這事只怕還有後患，一不做二不休，索性殺了這兩個雛兒，發筆橫財再說，突然抽出單刀向張康劈去。張康大駭，急忙縮頭，一刀從頭頂掠過，砍去了他帽子。他挺身擋住公差，叫道：「公子快逃。」張朝唐轉身就奔。
那公差反手又是一刀，這次張康有了防備，側身閃過，仍是沒給砍中。主僕兩人沒命價奔逃。四名公差手持兵刃，吆喝著追來。
張朝唐平時養尊處優，加上心中一嚇，那裏還跑的快，眼見就要給公差追上，忽然迎面一騎馬奔馳而來。那中年公差見有人來，高聲叫道：「反了，反了，大膽盜賊，竟敢拒捕？」另外幾名公差也大叫：「捉強盜，捉強盜。」他們誣陷張朝唐主僕是盜匪，心想殺了人誰敢前來過問？
迎面那乘馬越奔越近。馬上乘客眼見前面兩人奔逃，後面四名公差大呼追逐，只道真是捉拿強人，催馬疾馳，奔到張朝唐主僕之前，俯身伸臂，一手一個，拉住兩人後領，提了起來。四名公差也已氣喘喘的趕到。
馬上乘者把張朝唐主僕二人往地上一擲，笑道：「強盜捉住了。」跳下馬來。這人身材魁梧，聲音洪亮，滿臉濃鬚，約莫四十來歲年紀。
四名公差見他身手矯捷，氣力甚大，當下含笑稱謝，將張朝唐主僕拉了起來。
那乘馬客見張朝唐一身儒服，張康青衣小帽，是個書僮，那裏像是強盜，不禁一怔。張康叫了起來：「英雄救命！他們要謀財害命。」那人喝問：「你們幹甚麼的？」張康叫道：「這是我家公子，是去廣州趕考……」話未說完，已被一名公差按住了嘴。
那中年公差向乘馬客道：「老兄，你走你的道吧，莫管我們衙門的公事。」乘馬客道：「你放開手，讓他說。」張朝唐道：「在下一介書生，手無縛雞之力，豈是強人……」一名公差喝道：「還要多嘴？」反身一記巴掌，向他打去。
乘馬客馬鞭揮出，鞭上革繩捲住公差手腕，這一掌便未打著。乘馬客問道：「到底怎麼回事？」張康道：「我家公子要去廣州考秀才，遇上這四人。他們見到我們的銀子，就想殺人。」說到這裏，跪下叫道：「英雄救命！」
乘馬客問公差道：「這話可真？」眾公差冷笑不答。那老王站在他背後，乘他不覺，突然舉刀摟頭砍將下來。
乘馬客聽得腦後風生，更不回頭，身子向左微挫，右足「烏龍掃地」，橫掃而出，正中老王足脛，將他踢出數步。餘下三名公差大叫：「真強盜來啦。」兩個舉起鐵尺，一個揮動鐵鏈，向乘馬客圍攻過來。
張朝唐見他手無寸鐵，不禁暗暗擔憂。乘馬客卻挺然不懼，左躲右閃，三名公差的兵刃始終傷他不著。那老王站起身來，掄刀上前夾攻。乘馬客大喝一聲，老王吃了一驚，一刀沒砍準，乘馬客劈面一拳，打得他鼻血直流。老王只顧護痛，雙手掩面，噹啷一聲，手中單刀跌落在地。乘馬客搶過單刀，回手揮出，砍中了一名手持鐵尺的公差右肩。他兵刃在手，如虎添翼，刀光閃處，手持鐵鏈的公差左腿中刀，跌倒在地。賸下一名公差不敢再戰，不顧同伴死活，和老王兩人撒腿就逃。乘馬客哈哈大笑，將單刀往地下一擲，躍上馬背。
張朝唐忙上前道謝，請問姓名。乘馬客見兩名公差躺在地上哼哼唧唧的叫痛，向他怒目而視，說道：「這裏不是說話之所，咱們上馬再談。」張康拿回包裹，牽過馬來，三人並轡而行。
張朝唐說了家世姓名。乘馬客道：「原來是張公子。在下姓楊，名鵬舉，江湖上人稱摩雲金翅，是武會鏢局的鏢頭。」張朝唐道：「今日若非閣下相救，小弟主僕兩人準是沒命的了。」
楊鵬舉道：「這一帶亂的著實厲害，兵匪難分，公子還是及早回去外國的為是。在下也正要去廣州，公子若不嫌棄，咱們便可結伴而行。」
張朝唐大喜，一再稱謝。這幾日來他嚇得心神不定，現今得和一位鏢客同行，適才又見到他武功了得，登時大感心安。
三人行了二十幾里路，尋不到打尖的店家。楊鵬舉身上帶著乾糧，取出來分給兩人吃了。張康找到個破瓦罐，撿了些乾柴，想燒些水來喝，忽轉得身後有人大叫：「強盜在這裏了！」張康嚇了一跳，手一震，把瓦罐中的水都潑在柴上。
楊鵬舉回過頭來，只見剛才逃走的公差一馬當先，領了十多名軍士，騎了馬趕來。楊鵬舉叫道：「快上馬。」三人急忙上馬。楊鵬舉讓二人先走，抽出掛在馬鞍旁的單刀，在後掩護。眾軍士高叫：「捉強盜哪！」縱馬急追。
楊鵬舉等逃出一程，見追兵越趕越近，軍士紛紛放箭。楊鵬舉揮刀撥打，忽見前面有條岔路，叫道：「走小路！」張朝唐縱馬向小路馳去，張康和楊鵬舉跟隨在後，追兵毫不放鬆。那公差大嚷：「追啊，抓到了強盜，大夥兒分他金銀。」
楊鵬舉見追兵將近，索性勒轉馬來，大喝一聲，揮刀砍去。那公差嚇得倒退，其餘軍士卻挺槍攢刺。楊鵬舉敵不過人多，混戰中腿上中了一槍，傷勢雖然不重，卻已不敢戀戰，雙腿一夾，提韁縱馬向前急衝，揮刀將一名軍士左臂砍斷。其餘軍士嚇得紛紛後退，楊鵬舉已回馬疾馳。眾軍士見他逃跑，膽氣又壯，吶喊追來。
不一刻楊鵬舉已追上張氏主僕，這時道路愈來愈窄，眾軍士畏懼楊鵬舉勇猛，不敢十分逼近。
三人縱馬奔跑了一陣，山道彎彎曲曲，追兵吶喊之聲雖然清晰可聞，人影卻已不見。急馳中前面突然出現三條小岔路，楊鵬舉低喝：「下馬！」三人把馬牽到樹叢中躲了起來，片刻間追兵也已趕到。那公差畧一遲疑，領著軍士向一條岔路趕了下去。
楊鵬舉道：「他們追了一陣不見，必定回頭。咱們快走。」撕下衣襟裹好腿傷，三人向另一條岔路急馳而去。
過不多久，後面追兵聲又隱隱傳來，楊鵬舉甚是惶急，見前面有三間瓦屋，屋前有一個農夫正在鋤地，便下馬走到農夫身前，說道：「大哥，後面有官兵要害我們，請你找個地方給躲一躲。」那農夫只管鋤地，便似沒聽見他說話。張朝唐也下馬央告。
那農夫突然抬起頭來，向他們從頭至足打量。就在這時，前面樹叢中傳來牛蹄踐土之聲，一個牧童騎在牛背上轉了出來。那牧童約莫十歲上下年紀，頭頂用紅繩紮了個小辮子，臉色黝黑，一雙大眼卻是炯炯有神。那農夫對牧童道：「你把馬帶到山裏去放草，天黑了再回來吧。」小牧童望了張朝唐三人一眼，應道：「好！」牽了三匹馬就走。
楊鵬舉不知那農夫是甚麼用意，可是他言語神情之中，似有一股威勢，竟然不敢出言阻止牧童牽馬。這時追兵聲更加近了，張朝唐急的連說：「怎麼辦，怎麼辦？」
那農夫道：「跟我來。」帶領三人走進屋內。廳堂上木桌板凳，牆上掛著簑衣犁頭，但收拾得甚是潔淨，不似尋常農家。那農夫直入後進，三人跟了進去，走過天井，來到一間臥房。那農夫撩起帳子，露出牆來。伸手在牆上一推，一塊大石翻了進去，牆上現出一個洞來。那農夫道：「進去吧！」
三人依言入內，原來是個寬敞的山洞。這屋倚山而建，剛造在山洞之前，如不把房屋拆去，誰也猜不到有此藏身之所。
三人躲好，那農夫關上密門，自行出去鋤地。不一刻，公差已率領軍士追到。那老王向農夫大聲吆喝：「喂，有三個人騎馬從這邊過去嗎？」那農夫向小路的一邊指了一指，道：「早就過去啦！」
公差軍士奔出了七八里地，不見張朝唐等蹤迹，掉轉馬頭，又來詢問。那農夫裝聾作啞，話也說不大清楚。一名軍士罵道：「他媽的，多問這傻瓜有屁用？走吧！」一行人又向另一條岔路追了下去。
張朝唐和楊鵬舉、張康三人躲在山洞之內，隱隱聽得馬匹奔馳之聲，過了一會，聲音聽不見了，那農夫始終不來開門。楊鵬舉焦躁起來，使力推門，推了半天，石門紋絲不動。三人只得坐在地上打盹。楊鵬舉創口作痛，不住咒罵公差軍士。
也不知過了幾個時辰，石門忽然軋軋作響的開了，透進光來。那農夫手持燭台，說道：「請出來吃飯吧。」
楊鵬舉首先跳起，走了出去，張氏主僕隨後走到廳上。只見板桌上擺了熱騰騰的飯菜，大盆青菜豆腐之外，居然還有兩隻肥雞。楊鵬舉和張康都暗暗歡喜。
廳上除了日間所見的農夫和牧童，還有三人，都作農夫打扮。張朝唐和楊鵬舉拱手相謝，道了自己姓名，又請問對方姓名。
一個面目清癯、五十來歲的農夫道：「小人姓應。」指著日間指引他們躲藏的人道：「這位姓朱。」一個身材極高的瘦子自稱姓倪，一個肥肥矮矮的則說姓羅。張朝唐道：「我還道各位是一家人，原來均非同姓。」那姓應的道：「我們都是好朋友。」
張朝唐見他們說話不多，神色凜然，舉止端嚴，絕不似尋常農夫。那姓朱和姓倪的尤具威猛之氣，姓應的則氣度高雅，似是位飽讀詩書的士人。張朝唐試探了幾句，姓應的唯唯否否，並不接口。
飯罷，姓應的問起官兵追逐的原因，張朝唐原原本本說了。他口才便給，描述途中所見慘況，以及公差欺壓百姓、誣良為盜的種種可惡情狀，說來有聲有色。那姓倪的氣得猛力在桌上一拍，鬚眉俱張，開口欲罵。姓應的使個眼色，他就不言語了。
張朝唐又說到楊鵬舉如何出手相援，把他大大的恭維了一陣。楊鵬舉十分得意，說道：「這算得甚麼，想當年在江西我獨力殺死鄱陽三兇，那才教露臉呢。」當下便縱談當時情勢如何危急、自己如何英勇、如何敗中取勝、說得口沫橫飛。他越說越得意，將十多年來在江湖上的遭遇大吹特吹，加油添醬，說得自己英雄蓋世，當世無敵，又說道上強人怎樣見了他從來不敢招惹。正說得高興，那小牧童忽然嗤的一聲笑。
楊鵬舉橫了他一眼，也不在意，不住口的談論江湖上的事蹟。張朝唐對這些事聞所未聞，聽得很有興味，張康更是小孩脾氣，連連驚嘆詢問。
楊鵬舉後來說到了武技，舉手抬足，一面講一面比劃。幾個農夫卻似乎聽的意興索然，姓羅的胖子打了個呵欠道：「不早啦，大家睡吧！」
小牧童過去關上了門，姓朱的從暗處提出一塊大石，放在門後。楊鵬舉一見之下，不由得倒抽了一口涼氣，暗道：「這人好大力氣，這塊石頭少說也有四百來斤，他居然毫不費力的提來提去。」
姓應的見他面色有異，說道：「山裏老虎多，有時半夜裏撞進門來，因此要用石頭堵住門戶。」說聲未畢，忽然一陣狂風吹來，樹枝呼呼作響，門窗俱動，隨即聽到虎嘯連聲，甚是猛惡，接著門外牛馬驚嘶起來。姓應的道：「說到曹操，曹操就到。」
姓倪的站起身來，從門背後取其一柄鋼叉，嗆啷啷一抖，說道：「今兒不能讓牠逃走了。承志，你也去。」小牧童喜形於色，大聲答應，奔進右邊屋裏，隨即出來，手上多了個皮囊和一枝短鐵槍。姓朱的提開大石，一陣狂風砰的一聲把門吹開，風夾落葉，直捲進來，蠟燭頓時熄滅。張康驚叫聲中，姓倪的和小牧童先後縱出門去。
楊鵬舉提起單刀，說道：「我也去！」剛跨出一步，忽然左腕被人握住，他用力一掙，那知握住他的五指直如一把鋼爪，將他牢牢扣住，絲毫動彈不得。黑暗中聽得那姓朱的說道：「別出去，大蟲很厲害。」楊鵬舉又是往外一奪。那姓朱的沒給他拉動，也沒更向裏拉，只是抓著不放。楊鵬舉無可奈何，只得坐了下來，姓朱的也就鬆開了手。
只聽得門外那姓倪的吆喝聲、虎嘯聲、鋼叉上鐵環的嗆啷聲、疾風聲、樹枝墮地聲，響成一片，偶然還夾著小牧童清脆的呼叫聲，兩人一虎，顯是在門外惡鬥。過了一會，聲音漸遠，似乎那虎受創逃走，兩人追了下去。
姓羅的拿出火石火絨點燃了蠟燭，只見屋中滿地都是樹葉。張康早嚇得臉無人色，張朝唐和楊鵬舉也是驚異不定。
眾人在寂靜中不作一聲，過了半晌，遠處腳步聲響，轉瞬間小牧童衝進屋來後，笑逐顏開的叫道：「吃老虎肉，吃老虎肉！」
張朝唐見他短槍頭上鮮血淋漓，心想他小小年紀，居然如此武勇，自己手無縛雞之力，實在慚愧。
正思念間，只見那姓倪的大踏步的走進來，左手持鋼叉，右手提著黃黑相間的一隻大老虎。他將老虎往地下一擲，張朝唐嚇了一跳，不由自主的往裏一縮，瞧那老虎一動也不動，才知已被打死。
那姓倪的臉色鄭重，向小牧童道：「承志，剛才你打錯了，知道嗎？」小牧童低下了頭道：「嗯，我不該正面對著大蟲放鏢。」姓倪的這才和顏悅色的道：「正面放鏢，也不是不可以，不過你鋼鏢脫手之後，須得立時往橫裏跳開。剛才你一鏢打壞牠一隻眼睛，卻站著不動。大蟲負痛之後，撲過來的勢道更猛，不是我一叉抵住，你這條小命還在嗎？」小牧童不敢作聲。姓倪的又讚他幾句：「你這幾枝鏢準頭是很不錯的了，只是力道欠著一點，不過這也不能怪你，將來年紀大了，腕力自會加添。」提起那隻大老虎，指著老虎糞門上的一枝鏢，說道：「這一鏢要是勁道足，打進牠肚裏，已夠要了這畜牲的命啦。」小牧童道：「明兒我要用心練。」姓倪的點點頭，把老虎拖進後堂。
楊鵬舉見這兩人這般輕而易舉的殺了這一頭大老虎，心下惴惴，看來這批人路道著實不對，多半是喬裝的大盜，自己和張氏主僕胡裏胡塗的自投盜窟，這番可當真糟了。張朝唐卻不以為意，極力稱讚小牧童的英勇，撫著他的手問道：「小兄弟姓甚麼？你名叫承志，是不是？」那牧童笑而不答。
當晚張朝唐和楊鵬舉、張康三人同處一室。張康著枕之後立即酣睡。張朝唐想起此行風波萬里，徒然擔驚受怕，不知此去廣州，是否尚有凶險，又想浡泥國老虎也是不少，卻無如此厲害的殺虎英雄，中土人物，畢竟不凡，思潮起伏，一時難以入睡。過了一會，忽聽得書聲朗朗，那小牧童讀起書來。
張朝唐側耳細聽，書聲中說的似是兵陣戰鬥之事，不禁好奇心起，披衣下床，走到廳上。只見桌上燭光明亮，小牧童正自讀書。姓應的坐在一旁教導，見他出來，只向他點了點頭，又低下頭來，指著書本講解。
張朝唐走近前去，見桌上還放了幾本書，拿起來一看，書面上寫著「紀效新書」四字，原來是本朝戚繼光將軍所著的兵法。戚繼光之名，張朝唐在浡泥國也有所聞，知道是擊破倭寇的名將，後來鎮守薊州，強敵不敢犯邊，用兵如神，威震四海。
張朝唐向姓應的道：「各位決計不是平常人，卻不知何以隱居在此，可能見告麼？」姓應的道：「我們是尋常老百姓，種田打獵，讀書識字，那是最平常不過的。公子為何覺得奇怪？難道只有官家子弟才可以讀書嗎？」張朝唐心想：「原來中土尋常農夫，也是如此文武全才，果非蠻邦之人可比。」心下甚是佩服，說了聲「打擾」，又回房睡去了。
朦朦朧朧的睡了一回，忽覺有人相推，驚醒坐起，只聽楊鵬舉低聲道：「這裏果然是盜窟，咱們快走吧！」張朝唐大吃一驚，低問：「怎麼樣？」
楊鵬舉點燃燭火，走到一隻木箱邊，掀起箱蓋道：「你看。」
張朝唐一看，只見滿箱盡是金銀珠寶，一驚之下，做聲不得。
楊鵬舉把燭台交他拿著，搬開木箱，下面又有一隻木箱，伸手便去扭箱上銅鎻。張朝唐道：「別看旁人隱私，只怕惹出禍來。」楊鵬舉道：「這裏氣息古怪。」張朝唐忙問：「甚麼氣息？」楊鵬舉道：「血腥氣。」張朝唐便不敢言語了。
楊鵬舉扭斷了鎻，靜聽房外沒有動靜，輕輕揭開箱蓋，把燭台往箱內一照，兩人登時嚇得目瞪口呆。
但見箱中赫然是兩顆首級，一顆砍下時日已久，血迹都已變成黑色，另一顆卻是新斬下的。兩顆首級都用石灰、藥料製過，是以鬚眉俱全，那顆砍下已久的也未腐爛。楊鵬舉饒是久歷江湖，這時也嚇得手腳發軟，張朝唐那裏還說得出話來。
楊鵬舉輕輕把箱子還原放好，說道：「快走！」到炕上推醒了張康，摸到廳上。三人躡足走到門邊，楊鵬舉摸到大石，心中暗暗叫苦，竭盡全力，也搬它不動，剛只推開尺許，忽然火光閃亮，那姓朱的拿著燭台走了出來。
楊鵬舉手按刀柄，明知不敵，身處此境，也只有硬起頭皮一拚。那知姓朱的並不理會，說道：「要走了嗎？」伸手把大石提在一邊，打開了大門。
楊鵬舉和張朝唐不敢多言，喃喃謝了幾句，低頭出門，上馬向東疾馳。
奔了十幾里地，料想已脫險境，正感寬慰，忽然後面馬蹄聲響，有人厲聲叫道：「喂，站住，站住！」三人那裏敢停，縱馬急行。
突然黑影一幌，一人從馬旁掠過，搶在前面，手一舉，楊鵬舉坐騎受驚，長嘶一聲，人立起來。楊鵬舉揮刀向那人當頭砍去。那人空手拆了數招，忽地高躍，伸左拳向楊鵬舉右太陽穴打落。楊鵬舉單刀「橫架金樑」，向他手臂疾砍。豈知那人這一拳乃是虛招，半路上變拳為掌，身未落地，已勾住楊鵬舉手腕，喝聲：「下來！」將他拖下馬來，順手奪過了他手中單刀，擲在地下。
星光熹微中看那人時，正是那姓朱的農夫。
那人冷冷的道：「回去！」回過身來，騎上馬當先就走，也不理會三人是否隨後跟來。楊鵬舉知道反抗固然無益，逃也逃不了，只得乖乖的上了馬，三人跟著他回去。
一進門，只見廳上燭火明亮，那小牧童和其餘三人坐著相候，神色肅然，一語不發。
楊鵬舉自忖不免一死，索性硬氣一點，昂然說道：「楊大爺今日落在你們手中，要殺就殺，不必多說。」
姓朱的道：「應大哥，你說怎麼辦？」姓應的沉吟不語。姓倪的道：「張公子主僕放走，把姓楊的宰了。」姓應的道：「這姓楊的幹保鏢生涯，做有錢人走狗，能是甚麼好人？但他今天見義勇為，總算做了件好事，就饒他一命。羅兄弟，把他兩個招子廢了。」
姓羅的站起身來，楊鵬舉慘然變色。
張朝唐不懂江湖上的說話，不知「把招子廢了」便是剜去眼睛之意，但見了各人神情，想來定是要傷害楊鵬舉，正想開口求情，那小牧童道：「應叔叔，我瞧他怪可憐的，就饒了他吧！」
姓應的與眾人對望了一眼，頓了一頓，對楊鵬舉道：「旣然有人給你求情，也罷，你能不能立一個誓，今晚所見之事，決不洩漏一言半語？」
楊鵬舉大喜，忙道：「今晚之事，在下實非有意窺探，但旣然被我見到了，自怪楊某有眼無珠，不識各位英雄好漢。各位的事在下立誓守口如瓶，將來如違此誓，天誅地滅，死得慘不堪言。」姓應的道：「好，我們信得過你是一條漢子，你去吧。」楊鵬舉一拱手，轉身要走。姓倪的突然站起來，厲聲喝道：「就這樣走麼？」
楊鵬舉一楞，懂了他的意思，慘然一笑，說道：「好，請借把刀給我。」姓朱的從桌下抽出一把利刃，輕輕倒擲過去。楊鵬舉伸手接住，走近幾步，左手平放桌上，颼的一刀，登時砍下三個手指，笑道：「光棍一人作事一身當，這事跟張公子全沒干係……」
眾人見他手上血流如注，居然還硬挺住，也都佩服他的氣概。姓倪的大拇指一挺，道：「好，今晚的事就這般了結。」轉身入內，拿出刀傷藥和白布來，給他止血，縛了傷口。
楊鵬舉不願再行停留，轉身對張朝唐道：「咱們走吧。」
張朝唐見他臉色慘白，自是痛極，想叫他在此休息一下，可是又說不出口。
姓應的道：「張公子來自萬里之外，我們驚嚇了遠客，很是過意不去，別讓你回到外國，說我們中土人士都是窮凶極惡之輩。這位楊朋友也很夠光棍。我送你這個東西吧。」說著從袋裏掏出一塊東西，交給張朝唐。
張朝唐接過一看，輕飄飄的是一塊竹牌，上面烙了「山宗」兩字，牌背烙了一些花紋，看不出有甚麼用處。
姓應的道：「眼前天下大亂，你一個文弱書生不宜在外面亂走，我勸你趕快回家。這幾天在路上要是遇上甚麼危難，拿出這塊竹牌來，或許有點兒用處。過得幾年……唉，或者是十年，二十幾年，你聽得中土太平了，這才再來吧！亂世功名，得之無益，反足惹禍。」
張朝唐再看竹牌，實不見有何奇特之處，不信它有何神秘法力，想是吉祥之物，隨口謝了一聲，交給張康收在衣囊之中。三人告辭出來，騎上馬緩緩而行。回到適才和那姓朱的交手所在，見單刀兀自在地，閃閃發光，楊鵬舉拾了起來，心想：「我自誇英雄了得，碰在人家手裏，屁也不值！」
天明時，到了一個小市鎮上，張朝唐找了客店，讓楊鵬舉安睡了一天一晚。次晨才再趕路。行到中午時分，打過尖，上馬又行了二十多里路，忽然蹄聲響處，一騎馬迎面奔來，掠過身旁，向三人望了一眼，絕塵而去。行了五六里路，後面馬蹄聲又起，仍是那騎馬追了上來。這次楊鵬舉和張朝唐都看得清楚了，馬上那人青巾包頭，眉目之間英悍之氣畢露，從三人身旁掠過，疾馳而前。
張朝唐道：「這人倒也古怪，怎麼去了又回來。」楊鵬舉道：「張公子，待會你自行逃命罷，不用等我。」張朝唐驚道：「怎麼？又有強盜麼？」楊鵬舉道：「走不上五里，必有事故，不過咱們後無退路，也只有向前闖了。」
三人惴惴不安，慢慢向前挨去，只走了兩里多路，只聽見噓哩哩一聲，一枝響箭射上天空，三乘馬向林中竄出，攔在當路。
楊鵬舉催馬上前，抱拳說道：「在下武會鏢局姓楊，路經貴地，並非保鏢，沒向各位當家投帖拜謁。這位張相公來自外國，他是讀書人，請各位高抬貴手，讓一條道。」他在江湖上本來畧有名頭，手上武藝也自不弱，不過剛斷了手指，又想這一帶道上的朋友多半與姓應的是一夥，是以措詞謙恭，好言相求。
三乘中當中一人雙手空空，笑道：「我們少了盤纏，要借一百兩銀子。」他說的是浙南土話，楊鵬舉和張朝唐愕然相對，不知他說些甚麼。
剛才騎馬來回相探的那人喝道：「借一百兩銀子，懂了沒有？」楊鵬舉見他們如此無禮，不禁大怒，喝道：「要借銀子，須憑本事！」當先那人喝道：「好！這本事值不值一百兩銀子？」從背上取下彈弓，叭叭叭，三粒彈子打上天空，等彈子勢完落下，又是連珠三彈，六顆彈子在空中分成三對，互相撞得粉碎，變成碎泥紛紛下墮。
楊鵬舉見到這神彈絕技，剛只一呆，突覺左腕劇痛，單刀噹的一聲落在地下，才知已被他彈子打中了手。
對面第三人手持軟鞭，縱馬過來，一招「枯藤纏樹」，向他腰間盤打而至。楊鵬舉勒馬避開。那人軟鞭鞭頭乘勢在地下捲起單刀，抄在手中，長笑一聲，縱馬疾馳，掠過張康身邊時，白光閃動，鋼刀揮了兩揮，已割斷他背上包裹兩端的布條。他卻毫不停留，催馬向前奔馳。
包裹正從張康背上滑落，打彈子那人恰好馳到，手臂探出，不待包裹落地，已俯身提起，掂了掂重量，笑道：「多謝了。」轉眼間三人跑得無影無蹤。
楊鵬舉只是嘆氣，無話可說。張康急道：「我們的盤費銀兩都在包裹，這……這……怎麼回家呢？」楊鵬舉道：「留下你這條小命，已算不錯的啦，走著瞧吧。」三人垂頭喪氣的又行。
走不到一頓飯時分，忽然身後蹄聲雜沓，回頭一望，只見塵頭起處，那三人又追了轉來。楊鵬舉和張朝唐都倒抽一口涼氣，心想：「搶了金銀也就罷了，難道當真還非要了性命不成？」
那三人馳到跟前，一齊滾鞍下馬，當先一人抱拳說道：「原來是自己人，得罪得罪。我們不知，多有冒犯，請勿見怪。」另一人雙手托住包裹，交給張康。張康卻不敢接，眼望主人。張朝唐點點頭，張康這才接了過來。
當先那人道：「剛才聽得這位言道，一位是楊鏢頭，一位是張公子，都是真姓麼？」張朝唐道：「正是！」說了兩人的姓名來歷。
三人聽了，均有詫異之色，互相望了一眼。當先那人說道：「在下姓黃，這兩位是親兄弟，姓劉。張公子，你早拿出竹牌來就好了，免得我們無禮。」張朝唐聽了這話，才知道這塊竹牌果真效力不小，心神不定之際，也不知說甚麼話好。
那姓黃的又道：「兩位一定也是到聖峯嶂去了，咱們一路走吧。」
張朝唐和楊鵬舉都料想他們是一幫聲勢浩大的盜夥，遠避之惟恐不及，怎敢再去招惹？張朝唐道：「我和這位朋友要趕赴廣州，聖峯嶂是不去了。」
姓黃的臉帶怒色道：「再過三天就是八月十六，我們千里迢迢的趕來粵東，你們到了這裏，怎不上山？」上山做甚麼，八月十六有甚麼干係，張朝唐和楊鵬舉兩人全不知情，可是又不敢直認。張朝唐硬了頭皮，說道：「兄弟家有急事，須得馬上回去。」
姓黃的怒道：「上山也躭擱不了你兩天。你們過山不拜，算得甚麼山宗的朋友？」張朝唐更加摸不著頭腦，不知道「山宗」是甚麼東西。
楊鵬舉終究閱歷多，見這情勢，知道聖峯嶂是非去不可的了，雖有凶險，也只有聽天由命，而且瞧他們神色語氣，也似並無惡意，便道：「三位旣然如此美意，我和張公子同上山去便是。」說著向張朝唐使個眼色，示意不可違拗。
姓黃的霽然色喜，笑道：「本來嘛，我想你們也不會這般不顧義氣。」
六人結伴同行，一路打尖住店，都由那姓黃的出頭，他只做幾個手勢，說了幾句古裏古怪的話，沿途飯館客店便都不收錢，而且招待得加意的周到客氣。
走了兩天，將近聖峯嶂山腳，只見沿途勁裝結束之人絡繹不絕，都是向聖峯嶂而去，肥瘦高矮，各色各樣的人都有，神色舉止，顯得都是武人。這些人與姓黃的以及劉氏兄弟大半熟識，見了面就執手道故。
張楊兩人抱定宗旨決不再窺探別人隱私，見他們談話，就站得遠遠的，但聽這些人招呼的聲音南腔北調，遼東河朔、兩湖川陝各地都有。瞧他們的行裝打扮，大都是來自遠地，人人都是風塵僕僕。張楊兩人暗暗納罕，又是慄慄危懼。
楊鵬舉心想：「看來這些人是各地山寨的大盜，多半是要聚眾造反。我是身家清白的良民，跟反賊們混在一起，走又走不脫，真是倒霉之極了。」
這天晚上，張朝唐等歇在聖峯嶂山腳下的一所店房裏，待次日一早上山。眾人正要吃晚飯，忽然一人奔進店來，叫道：「孫相公到啦！」此言一出，店中客人十之八九都站了起來，湧出店去。楊鵬舉一扯張朝唐的衣袖，說道：「瞧瞧去。」
走出店房，只見眾人夾道垂手肅立，似在等甚麼人。過了一陣，西面山道上傳來一陣馬蹄聲，眾人都提高了腳跟張望，只見一個四十來歲的書生騎在馬上，緩緩而來。他見眾人站在道旁迎接，催馬快行，馳到跟前，跳下馬來。人羣中一名大漢搶上前去，挽住馬韁。
那書生一路過來，和眾人逐一點頭招呼。他走到張朝唐跟前，見他也是書生打扮，微微一愕，雙手一拱，問道：「這位是誰？」張朝唐道：「在下姓張，請教閣下尊姓大名。」那書生道：「在下姓孫，名仲壽。」張朝唐拱手說道：「久仰，久仰。」孫仲壽微微一笑，進店房去了。
晚飯過後，楊鵬舉低聲對張朝唐道：「這姓孫的書生相公顯是很有權勢。張公子，你去跟他說說，請他放咱們走。大家是讀書人，話總容易說的通。」
張朝唐心想不錯，踱到孫仲壽門口，咳嗽一聲，舉手敲門。只聽到房裏有誦讀詩文之聲，他敲了幾下，讀書聲就停了。
房門打開，孫仲壽迎了出來，說道：「客店寂寞，張兄來談談，最好不過。」張朝唐一揖進去，見桌上放著一本攤開手抄書本，一瞥之下，見寫著「遼東」、「寧遠」、「臣」、「皇上」等等字樣，似是一篇奏章。張朝唐只怕又觸人所忌，不敢多看，便坐了下來。
孫仲壽先請問他家世淵源，張朝唐據實說了。孫仲壽說道：「張兄這番可來得不巧了。中華朝政糜爛，不知何日方得清明。以兄弟之見，張兄還是暫回浡泥，俟中華聖天子在位，再來應試的為是。」張朝唐稱是，說道正要歸去。接著把自己如何躲避官差、楊鵬舉如何相救、如何得到竹牌等事說了一遍，只是夜中見到箱內人頭一事畧去不提。
孫仲壽道：「我們在此相遇，可算有緣。明日張兄隨小弟上山。也好知道我中土的一件千古奇寃。只要此行所見所聞，不向外人洩露，小弟擔保張兄決無危害。」張朝唐謝了，卻不敢多問。
孫仲壽問起浡泥國人的風土人情，聽張朝唐所述，皆是聞所未聞，喟然說道：「不知幾時我中華百姓才得如浡泥國一般，安居樂業，不憂溫飽，共享太平之福？」
兩人直談到二更天時，張朝唐才告別回房。楊鵬舉已等得十分心焦，聽他轉告了孫仲壽之言，才放下了心。
次日正是中秋佳節，張朝唐、楊鵬舉和張康隨著大眾一早上山。中午時分，半山裏有十多人擔著飯菜等候，都是素菜，眾人吃了，休息一陣，繼續再行。
此後一路都有人把守，盤查甚嚴。查到張楊三人時，孫仲壽點一點頭，把守的人便不問了。張朝唐暗叫：「好險！要是昨晚沒跟他這一夕談話，今日是死是活，實所難料。」
傍晚時分，已到山頂，數百名漢子排隊相迎。中間一人身材魁梧，似是眾人的首領，見到孫仲壽上來，快步下來迎接，携手走入屋內。
山上疏疏落落有數十間房屋，最大的一座似是一所寺廟。這些屋宇模樣也甚平常，並無碉堡望樓等守禦設備，卻又不像是盜幫山寨。
楊鵬舉在山上見了眾人的勢派，料想山上建構必定雄偉威武，壁壘森嚴，那知渾不是這麼一回事，心下暗暗稱奇。他在江湖上混了十多年，見聞算得廣博，這一次卻半點摸不著頭腦。更有一件奇事，這些人萬里來會，瞧各人神情親密，都是知交好友，但相見時卻殊無歡愉之意，每人神色間都顯得十分悲戚憤慨。
張楊三人被引進一間小房，一會兒送進飯菜。四盤都是素菜，還有二十多個饅頭。當晚張朝唐和楊鵬舉悄悄議論，猜不透這些人到底在幹甚麼，對孫仲壽所說「千古奇寃」云云，更是難明所指。
次日張楊二人起身後，用過早點，在山邊漫步，只見到處都是大漢。有的頭上疤痕累累，有的斷手折足，個個是身經百戰、飽歷風霜的模樣。張楊兩人怕生事惹禍，走了一會就回進房中，一直不再出去。這天整日吃的仍是素菜。楊鵬舉肚裏暗罵：「他媽的賊強盜死了老祖宗，叫老子吃這般嘴裏淡出鳥來的素菜。」
傍晚時分，忽聽得鐘聲鏜鏜。不久一名漢子走進房來，說道：「孫相公請兩位到殿上觀禮。」張楊二人跟他出去。張康也想跟去，那人手一擺，道：「小兄弟，你早些睡吧。」
張楊二人隨著他繞過幾間瓦屋，來到寺廟跟前。張朝唐抬頭一看，見一塊橫匾上寫著「忠烈祠」三個大字，心想：「原來是座祠堂，不知供的是誰？」隨著那漢子穿過前堂和院子，見兩旁陳列著兵器架子，架上刀槍斧鉞、叉矛戟鞭，十八般兵刃一應俱全，都擦的雪亮耀眼。
來到大殿，但見殿上黑壓壓的坐滿了人，總有兩三千之眾。張楊二人暗暗心驚，原來這荒山之上，竟聚集了這許多人。
張朝唐抬頭看時，只見殿中塑著一座神像，本朝文官裝束，但頭戴金盔，身穿緋袍，外加黃罩甲，左手捧著一柄寶劍，右手手執令旗。那神像臉容清癯，三絡長鬚，狀貌威嚴，身子微側，目視遠方，眉梢眼角之間，似乎微帶憂態。神像兩側供著兩排靈位。張朝唐隔的遠了，看不清楚神主上所書的名諱。大殿四壁掛滿了旌旗、盔甲、兵刃、馬具之類，旌旗或紅或藍，也有黃色鑲紅邊，有的是白色鑲紅邊。
張朝唐滿腹狐疑，但見滿殿人眾容色悲戚，肅靜無聲。忽然神像旁一個身材瘦長的漢子站了起來，點燭執香，高聲叫道：「致祭。」殿上登時黑壓壓的跪得滿地，張朝唐和楊鵬舉也只得跟著跪下。
孫仲壽越眾而前，捧住祭文朗誦起來。楊鵬舉不懂祭文中文謅謅的說些甚麼，張朝唐卻愈聽愈驚。
只聽得祭文文意甚是憤慨激昂，旣把滿清韃子罵了個狗血淋頭，而對當今崇禎皇帝竟也絲毫不留情面，說他「昏庸無道，不辨忠奸」、「剛愎自用，傷我元戎」、「自壞神州萬里之長城，甘為皇帝苗裔之罪人」。對當今皇上如此肆口痛詆，豈不是公然要造反了嗎？張朝唐聽得驚疑不定。那知祭文後面愈來愈兇，竟把崇禎皇帝的列祖列宗也罵了個痛快，甚麼「功勛蓋世而魏公被毒，底定中土而青田受酖」，那是說明太祖殺害徐達、藍玉、劉基等功臣之事；後來又罵神宗亂徵礦稅，荼毒百姓；熹宗任用奄璫，朝中清流君子，不是殺頭，便是入獄，如熊廷弼等守土抗敵大臣，都慘遭殺害。
這篇祭文理直氣壯，一字一句都打入張朝唐心坎裏去，他雖遠在外國，但中土大事，卻也知聞。祭文後半段卻是「我督師威震寧遠，殲彼巨酋」等一大段頌揚武功的文字，更後來又再痛罵崇禎殺害忠良。
張朝唐聽到這裏，才知道這神像原來是連破清兵、擊斃清太祖努爾哈赤、使清人聞名喪膽的薊遼督師袁崇煥。他抬頭再看，見那神像栩栩如生，雙目遠矚，似是痛惜異族入侵，佔我河山，傷我黎民，恨不能復生而督師遼東，以禦外侮。
這時祭文行將讀完，張朝唐卻聽得更加心驚，原來祭文最後一段是與祭各人的誓言，立誓：「並誅明帝清酋，以雪此千古奇寃，而慰我督師在天之靈。」祭文讀畢，贊禮的人唱道：「對督師神像暨列位殉難將軍神主叩首。」眾人俯身叩頭。
一個幼童全身縞素，站在前列，轉身伏在地下向眾人還禮。張朝唐和楊鵬舉又吃了一驚，原來這幼童便是那天所遇的殺虎牧童。
眾人叩拜已畢，站起身來，都是淚痕滿面，悲憤難禁。孫仲壽對張朝唐道：「張兄大才，小弟這篇祭文有何不妥之處，請予刪削。」張朝唐連稱：「不敢。」孫仲壽命人拿過文房四寶來，說道：「小弟邀張兄上山，便是要借重海外才子手筆，於我袁督師的勳業更增光華。也好教世人知道，袁督師蒙寃遭難，普天共憤，中外同悲，並非只是我們舊部的一番私心。」
張朝唐心想，你叫我上山，原來為此，不由得好生為難，袁崇煥被朝廷處死，是因崇禎胡塗昏庸，不明忠奸是非，聽信了奸臣和太監的挑撥，天下都知寃枉，自己在浡泥之時，也曾聽得幾個廣東商人痛哭流涕的說起過。但旣由皇帝下旨而明正典刑，再說寃枉，便是誹謗今上。皇帝若是知道了，一紙詔書來到浡泥國，連父親都不免大受牽累。可是孫仲壽旣這麼說，在勢又不能拒絕，情急之下，忽然靈機一動，想起在浡泥國時所看過的兩部小說，一部是「三國演義」，一部是「精忠岳傳」。他讀書有限，不能如孫仲壽那麼駢四驪六的大做文章，當下微一沉吟，振筆直書：「黃龍未搗，武穆蒙寃。漢祚待復，諸葛星殞。嗚呼痛哉，伏維尚饗。」他說的是古人，萬一這篇短短的祭文落入皇帝手中，也不能據此而定罪名。
孫仲壽本想他是一個海外士人，沒甚麼學問，也寫不出甚麼好句子來，只盼他稱讚幾句袁督師的功績，也就是了，待見他寫下了這六句，十分高興。張朝唐把袁崇煥比之於諸葛亮和岳飛，自是推崇備至，無以復加。清人為金人後裔，皆為女真族，滿清初立國時，國號便仍稱為「金」。岳飛與袁崇煥皆抗金有功而死於昏君奸臣之手，兩人才畧遭遇，頗有相同之處，倒不是胡亂瞎比的。
孫仲壽把這幾句話向眾人解釋了，大家轟然致謝，對張楊兩人神態登時便親熱得多，不再以外人相待了。孫仲壽道：「張兄文筆不凡，武穆諸葛這兩句話，榮寵九泉。小弟待會叫他們刻在祠堂旁邊的石上，要令後人得知，我們袁督師英名遠播，連萬里之外的異邦士民也盡皆仰慕。」張朝唐作揖遜謝。
各人叩拜已畢，各就原位坐下。那贊禮的人又喊了起來：「某某營某將軍」、「某某鎮某總兵」，喊了一個武將官銜，便有一人站起來大聲說話。張朝唐聽了官銜和言中之意，得知這些人都是袁崇煥的舊部，他被害之後，各人憤而離軍，散處四方，今日是袁督師遭難的三週年忌辰，是以在他故鄉廣東東莞附近的聖峯嶂相聚，祭奠舊主。聽他們話中之意，似乎尚有甚麼重大圖謀。
當贊禮人叫到「薊鎮副總兵朱安國」時，一人站了起來，張朝唐和楊鵬舉都心頭一震，原來這人便是引導他們躲入密室的那個農夫。楊鵬舉心想：「原來他是抗清的薊遼大將，那麼我敗在他手裏，也不枉了。」
只聽他朗聲說道：「袁公子這三年來身子壯健，武藝大有進步，書也讀了不少，我和倪、羅兩位兄弟的武功都已傳給了他，請各位另推明師。」孫仲壽道：「咱們兄弟中，還有誰武功更高得過你們三位的，朱將軍不必太謙。」朱安國道：「袁公子學武聰明的很，我們只稍加點撥，他馬上就會了。我們三個已經傾囊以授，的確要另請名師，以免躭誤他功夫。」孫仲壽道：「好吧，這事待會再議。誅奸的事怎麼了？」
那姓倪的殺虎英雄站起身來，說道：「那姓范的奸賊是羅參將前個月趕到浙江誅滅的。姓史的奸賊，十天前被我在潮州追到。兩人的首級在此。」說罷從地上提起布囊，取出兩個人頭來。
眾人有的轟然叫好，有的切齒痛罵。孫仲壽接過人頭，供在神像桌上。
張朝唐這才明白，他們半夜裏在箱中發現的人頭，原來是袁黨的仇人，那定是與陷害袁崇煥一案有關的奸人了。這時不斷有人出來呈獻首級，一時間神像前的供桌上擺了十多個人頭。聽這些人的稟報，人頭中有一個是當朝姓高的御史，他是魏忠賢的黨羽，曾誣奏袁崇煥通敵賣國，眾人對他憤恨尤深。
各人稟告完畢，孫仲壽說道：「小奸誅了不少，大仇卻尚未得報，韃子皇太極和昏君崇禎仍然在位。如何為大元帥報仇雪恨，各位有甚麼高見？」一個矮子站了起來，說道：「孫相公！」孫仲壽道：「趙參將有甚麼話請說。」那矮子說道：「依我說……」
剛說了三個字，門外一名漢子匆匆進來稟道：「李闖將軍派了人來求見。」眾人一聽，都轟叫起來。孫仲壽道：「趙參將，咱們先迎接闖軍的使者。」趙參將道：「對。」首先搶了出去，眾人都站起身來。
大門開處，兩條大漢手執火把，往旁邊一站，走進三個人來。楊鵬舉已久聞李闖的名頭，知他名叫李自成，這幾年來殺官造反，威勢極大，倒要看看他部下是何等英雄人物。
只見當先一人四十多歲年紀，滿臉麻皮，頭髮蓬鬆，身上穿一套粗布衫褲，膝蓋手肘處都已擦壞，到處打滿了補釘，腳下赤足，穿一雙草鞋，腿上滿是泥污，純是個莊稼漢模樣。他身後跟著兩人，一個三十多歲，皮膚白淨；另一個廿多歲，身材魁梧，面容黝黑，也是農夫模樣。這三人看上去忠厚老實，怎知他們竟是橫行秦晉的「流寇」。
當先那人走進大殿，先不說話，往神像前一站。那白臉漢子從背後包袱中取出香燭，在神像前點上，三人拜倒在地，磕起頭來。那小牧童在供桌前跪下磕頭還禮。
三人拜畢，臉有麻子的漢子朗聲說道：「我們李將軍知道袁督師在關外打韃子，立了大功，心裏很是佩服。後來袁督師被皇帝寃枉害死，天下老百姓都氣憤的很。李將軍派我們來代他向督師的神位磕頭。現今官逼民反，我們為了要吃飯，只好抗糧殺官。求袁大元帥英魂保佑，我們打到北京，捉住皇帝奸臣，一個個殺了，給大元帥和天下的老百姓報仇。」說完又拜了幾拜。
眾人見李自成的使者尊重他們督師，都心存好感，聽了他這番話，雖然語氣粗陋，卻是至誠之言。
孫仲壽上前作揖，說道：「多謝，多謝。請教高姓大名。」那漢子說道：「我叫劉芳亮。李將軍得知今日是袁大元帥忌辰，因此派我前來在靈前拜祭，並和各位相見。」孫仲壽道：「多承李將軍厚意盛情，在下姓孫名仲壽。」那白淨面皮的人道：「啊，你是孫祖壽將軍的弟弟。孫將軍和韃子拚命而死，我們一向是很敬仰的。」
孫祖壽是抗清大將，在邊關多立功勳，於清兵入侵時隨袁崇煥捍衞京師。袁崇煥下獄後，孫祖壽憤而出戰，在北京永定門外和大將滿桂同時戰死，名揚天下。孫仲壽文武全才，向為兄長的左右手，在此役中力戰得脫，憤恨崇禎寃殺忠臣，和袁崇煥的舊部散在江湖，撫育幼主，密謀復仇。他精明多智，隱為袁黨的首領。
孫祖壽慷慨重義，忠勇廉潔，「明史」上記載了兩個故事：
孫祖壽鎮守固關抗清時，出戰受傷，瀕於不起。他妻子張氏割下手臂上的肉，煑了湯給他喝，同時絕食七日七夜，祈禱上天，願以身代。後來孫祖壽痊愈而張氏卻死了。孫祖壽感念妻恩，終身不近婦人。
他身為大將時，有一名部將路過他昌平故鄉，送了五百兩銀子到他家裏。在當時原是十分尋常之事，但他兒子堅決不受。後來他兒子來到軍中，他大為嘉獎，請兒子喝酒，說：「不受贈金，深得我心。倘若你受了，這一次非軍法從事不可。」「明史」稱讚他「其秉義執節如此。」
孫仲壽為人處事頗有兄風，是以為眾所欽佩。

註：明成祖應浡泥國蘇丹之請，封其山為「長寧鎮國山」，親製碑文，並題詩一首，譯意如下：
「在熱帶的海上，是浡泥國所處的地方。人民親近仁義，只有歸順，沒有違逆。賢王勤懇謹慎，仰慕中華教化。大明管理外國的官員加以指導，就到中國來朝拜了，帶了你的妃子、世子、兄弟、陪臣，來到大明宮殿階下磕頭，陳奏道：『皇上就像是天一樣，將溫暖和愉樂普賜天下，對任何人都一樣眷顧，沒有偏愛，沒有歧視。』但我自己反省，德行不夠，沒有你所說的這樣偉大。你冒著風浪，遠涉重洋，乘船來到，實在是很辛苦。查考歷來遠邦的臣屬，歸順的時候就來朝拜，不服的時候就不來了，自己前來都不容易，何況還帶了家室？你國王秉志貞誠，像金石一樣堅固。西南各國的蕃邦君主，那一位能及得上你？你國內有一座巍峨的高山，鎮寧邦國。現在在石碑上刻了文字，以發揚你國王的美德。但願你國王美德光大，國泰民安，今後千秋萬歲，都歸附我大明。」

第二回 恩仇同患難 死生見交情

眾人正要敘話，劉芳亮的黑臉從人忽然從後座上直縱出去，站在門口。眾人出其不意，不知發生甚麼事，都站了起來。只見那黑臉少年指著人羣中兩個中年漢子喝道：「你們是曹太監的手下人，到這裏來幹甚麼？」
此言一出，眾人都大吃一驚，均知崇禎皇帝誅滅魏忠賢和客氏之後，宮中朝中逆黨雖然一掃而空，然而皇帝生性多疑，又秉承自太祖、成祖以來的習氣，對大臣多所猜忌，所任用的仍是從他信王府帶來的太監，其中最得寵的則是曹化淳。此人統率皇帝的御用偵探和衞士，即所謂「廠衞」，刺探朝中大臣和各地將帥的隱私，文武大臣往往不明不白的為皇帝下旨誅殺，或是任意逮捕，關入天牢，所謂「下詔獄」，都是由於曹化淳的密報。曹太監的名頭，當時一提起來，可說是人人談虎色變。
那兩人一個滿顋黃鬚，四十上下年紀，另一個卻面白無鬚，矮矮胖胖。那矮胖子面色倏變，隨即鎮定，笑道：「你是說我嗎？開甚麼玩笑？」黑臉少年道：「哼，開玩笑！你們兩個鬼鬼祟祟在客店裏商量，要混進山宗來，又說已稟告了曹太監，要派兵來一網打盡，這些話都給我聽見啦！」
黃鬚人拔出鋼刀，作勢便要撲上廝拚。那白臉胖子卻哈哈一笑，說道：「李闖想收併山宗的朋友，居心險惡，那一個不知道了？你想來造謠生事，挑撥離間，那可不成。」他說話聲又細又尖，儼然太監聲口，可是這幾句話卻也生了效。袁黨中便有多人側目斜視，對李自成的使者起了疑心。
劉芳亮雖出身農家，但久經戰陣，百鍊成鋼，見了袁黨諸人的神色，知道此人的言語已打動眾心，便即喝道：「閣下是誰？是山宗的朋友麼？」這句話問中了要害，那人登時語塞，只是冷笑。
孫仲壽喝道：「朋友是袁督師舊部麼？我怎地沒見過？你是那一位總兵手下？」
那白臉人知道事敗，向黃鬚人使個眼色，兩人陡地躍起，雙雙落在門口。黃鬚人揮刀向黑臉少年砍去。那白臉人看似半男半女，行動卻甚是迅捷，腕底一翻，已抽出判官雙筆，向黑臉少年胸口點到。
黑臉少年因是前來拜祭，為示尊崇，又免對方起疑，上山來身上不帶兵刃。眾人見他雙手空空，驟遭夾擊，便有七八人要搶上救援。不料那少年武功甚是了得，左手如風，施展擒拿手法，便抓黃鬚客的手腕，同時右手駢起食中兩指，搶先點向白臉人的雙目。這兩招遲發先至，立時逼得兩名敵人都退開了兩步。
袁黨眾人見他只一招之間便反守為攻，暗暗喝釆，俱各止步。那兩人見衝不出門去，知道身處虎穴，情勢凶險之極，剛退得兩步，便又搶上。黑臉少年使開雙掌，在單刀雙筆之間穿梭來去，攻多守少。那兩人幾次搶到門邊，都被他逼了回來。
白臉人心中焦躁，筆法一變，雙筆橫打豎點，招招指向對方要穴。黃鬚客施展山西武勝門刀法，矮下身子，疾砍黑臉少年下盤。眾人眼見危急，都想伸手相助，但一瞥眼間，見劉芳亮神色鎮定，反而坐下來觀戰，均想，他自己人尚且不急，定是有恃無恐，且看一下動靜再說。
三人在大殿中騰挪來去，鬥到酣處，黃鬚人突然驚叫一聲，單刀脫手向人叢中飛去。朱安國躍起伸手一抄，接在手中。就在此時，黑臉少年踏進一步，左腿起處，一腳把黃鬚人踢倒。他左腿尚未收回，右腿乘勢又起，白臉人吃了一驚，只想逼開敵人，奪門逃走下山，當下奮起平生之力，雙筆一先一後反點敵人胸口。黑臉少年右手陡出，抓住左筆筆端，使力一扭，已把一隻判官筆搶過。這時對方右筆跟著點到，他順手將筆梢砸了過去。雙筆相交，噹的一聲，火星交迸，白臉人虎口震裂，右筆跟著脫手。
黑臉少年一聲長笑，右手抓住他胸口，一把提起，左手扯住他的褲腰，雙手一分，只聽得嗤的一聲，白臉人一條褲子已被扯下來，裸出下身。眾人愕然之下，黑臉少年笑道：「你是不是太監，大家瞧瞧！」眾人目光全都集到那白臉人的下身，果見他是淨了身的。鬨笑聲中，眾人圍了攏來，眼見這黑臉少年出手奇快，武功高明之極，心下都甚敬佩。
這時早有人擁上去把白臉人和黃鬚人按住。孫仲壽喝問：「曹太監派你們來幹甚麼？還有多少同黨？怎麼能混進來的？」兩人默不作聲。孫仲壽一使眼色，羅參將提起單刀，呼呼兩刀把兩人首級割下，放在神像前的供桌上。
孫仲壽拱手向劉芳亮道：「若不是三位發現奸賊，我們大禍臨頭還不知道。」劉芳亮道：「那也是碰巧。我們在道上遇見這兩個傢伙，見他們神色古怪，身手又很靈便，晚上便到客店去查探，僥倖發覺了他們的底細。」
孫仲壽向劉芳亮的兩位從人道：「請教兩位尊姓大名。」兩人報了姓名，膚色白淨的叫田見秀，黑臉少年名叫崔秋山。朱安國過去拉住崔秋山的手，說了許多讚佩的話。
劉芳亮和孫仲壽及袁黨中幾個首腦人物到後堂密談。劉芳亮說道，李將軍盼望大家携手造反，共同結盟。袁黨的人均感躊躇。眾人雖然憎恨崇禎皇帝，決意暗中行刺，殺官誅奸之事也已作了不少，但人人本來都是大明命官，要他們造反，卻是不願，只求刺死崇禎後，另立宗室明君。何況李自成總是「流寇」，雖然名頭極大，但打家刦舍，流竄擄掠，幹的是強盜勾當，大家心中一直也不大瞧得起。袁黨眾人離軍之後，為了生計，有時也難免做幾樁沒本錢買賣，卻從來不公然自居盜賊。雙方身分不同，議論良久難決。
最後孫仲壽道：「咱們的事已給曹太監知道，如不和李將軍合盟以舉大事，不但刺殺崇禎給袁督師報仇之事難以成功，只怕曹太監還要派人到處截殺。咱們勢孤力弱，難免一一遭了毒手。劉兄，咱們這樣說定成不成？我們山宗幫李將軍打官兵，李將軍事成之後，須得竭力滅了滿洲韃子。咱們話又說明在先，日後李將軍要做皇帝，我們山宗朋友卻不贊成，須得由太祖皇帝的子孫姓朱的來做。」
劉芳亮道：「李將軍只是給官府逼不過，這才造反，自己是決計不做皇帝的，這件事兄弟拍胸擔保。人家叫我們流寇，其實我們只是種田的莊稼漢，只求有口飯吃，頭上這顆腦袋保得牢，也就是了。我們東奔西逃，那是無可奈何。憑我們這樣的料子，也做不來皇帝大官。至於打建州韃子嘛，李將軍的心意跟名位一模一樣，平時說起，李將軍對韃子實是恨到骨頭裏去。」
孫仲壽道：「那是再好也沒有了。」袁黨眾人更無異言，於是結盟之議便成定局。
裏面在商議結盟大計，殿上朱安國和倪浩拉著崔秋山的手，走到一個僻靜的角落裏。
朱安國道：「崔大哥，咱們雖是初會，可是一見如故，你別當我們是外人。」崔秋山道：「兩位大哥從前打韃子、保江山，兄弟一向是很欽佩的。今日能見到山宗這許多英雄朋友，兄弟實是高興得很。」倪浩道：「我冒昧請問，崔大哥的師承是那一位前輩英雄？」崔秋山道：「兄弟的受業恩師，是山西大同府一聲雷白野白老爺子。他老人家已去世多年了。」朱安國和倪浩互望了一眼，均感疑惑。倪浩說道：「一聲雷白老前輩的大名，我們是久仰的了。不過有一句話崔大哥請勿見怪。白老前輩武功雖高，但似乎還不及崔大哥。」崔秋山默然不語。朱安國道：「雖然青出於藍，徒弟高過師父的事也是常見，但剛才我看崔大哥打倒兩個奸細的身法手法，卻似另有真傳。」
崔秋山微一遲疑，道：「兩位是好朋友，本來不敢相瞞。我師父逝世之後，我機緣巧合，遇著一位世外高人。他老人家點撥了我一點武藝，要我立誓不許說他名號，所以要請兩位大哥原諒。」
倪朱兩人見他說得誠懇，忙道：「崔大哥快別這麼說，我們有一事相求，因此才大膽相問。」崔秋山道：「兩位有甚麼事，便請直言。大家是自己人，何必客氣？」朱安國道：「崔大哥請等一等，我們去找兩位朋友商量幾句。」
朱倪二人把那姓應和姓羅的拉在一邊。朱安國道：「這個崔兄弟武藝高強，咱們這裏沒一個及得上。聽他說話，性格也甚是豪爽。」倪浩道：「就是說到師承時有點吞吞吐吐。」於是把崔秋山的話覆述了一遍。
那姓應的名叫應松，是袁崇煥帳下的謀士，當年寧遠築城，曾出了不少力量。姓羅的名大千，是著名的炮手，寧遠一戰，他點燃紅夷大炮，轟死清兵無數，因功升到參將。
應松道：「咱們不妨直言相求，瞧他怎麼說？」朱安國道：「這事當先問過孫相公。」應松道：「不錯。」
轉到後殿，見孫仲壽和劉芳亮正談得十分投契，於是把孫仲壽請出來商量。這些武將所擅長的是行軍打仗，衝鋒陷陣，說到長槍硬弩，十盪十決，那是勇不可當，但武學中的拳腳器械功夫，卻均自知不及崔秋山。
孫仲壽道：「應師爺，這件事關係幼主的終身，你先探探那姓崔的口氣。」應松點頭答應，與朱安國、倪浩、羅大千三人同去見崔秋山。
應松道：「我們有一件事，只有崔大哥幫這個忙，所以……」
崔秋山見他們欲言又止，一幅好生為難的神氣，便道：「兄弟是粗人，各位有甚麼吩咐，只要兄弟做得到的，無不從命。」
應松道：「崔兄很爽快，那麼我們直說了。袁督師被害之後，留下一位公子，那時還只有七歲。我們跟昏君派來逮捕督師家屬的錦衣衞打了三場，死了七個兄弟，才保全袁督師這點骨血。」崔秋山嗯了一聲。應松道：「這位幼主名叫袁承志，由我們四人教他識字練武。他聰明得很，一教就會，這幾年來，我們的本領差不多都已傳授給他了。雖然他年紀小，功夫還不到家，但再跟著我們，練下去進境一定不大。」
崔秋山已明白他們的意思，說：「各位要他跟我學武？」朱安國道：「剛才見崔大哥出手殺賊，武功勝過我們十倍，要是崔大哥肯收這個徒弟，栽培他成材，袁督師在天之靈，定也感激不盡。」說罷四人都作下揖去。
崔秋山連忙還禮，沉吟道：「承各位瞧得起，兄弟本來不該推辭，不過兄弟現下是在李將軍軍中，來去無定，有時跟官軍接仗，也不知能活到那一天。要袁公子跟我在隊伍裏，一則怕我沒空教他，二則實在也太危險。」應松等均想這確是實情，心中好生失望。
崔秋山忽道：「有一人功夫勝我不知多少倍，如果他肯收袁公子，那真是袁公子的造化了。」忽又連連搖頭，自言自語：「不成，不成。」應松與朱安國忙問：「那是誰？」崔秋山道：「便是我先前說的那位奇人。這位前輩的功夫實在深不可測，他教了我兩個多月，兄弟只學到一點兒皮毛。」朱安國大喜，問道：「這位奇人是誰？」崔秋山道：「他老人家脾氣很是奇特，雖然教我武藝，可是不肯讓我叫他師父，也不准我向人洩露他姓名。求他老人家收袁公子為徒，只怕無法辦到。」倪浩問道：「這位奇人住在那裏？」崔秋山道：「他行蹤無定，到甚麼地方，也從來不和我說。」應松等四人眼見此事無望，只得作罷。
應松把袁承志叫了過來，和崔秋山見面。崔秋山見他靈動活潑，面貌黝黑，全無半分富貴公子嬌生慣養的情狀，很是喜歡。問他所學的武藝，袁承志答了，問道：「崔叔叔，你剛才抓住那兩個奸細，使得甚麼功夫？」崔秋山道：「那叫做伏虎掌法。」袁承志道：「這樣快，我看都看不清楚。」崔秋山笑道：「你想不想學？」袁承志一聽這話，忙道：「崔叔叔，請你教我。」
崔秋山向應松笑道：「我跟劉將軍說，在這裏躭幾天，就把這路掌法傳給他吧！」袁承志和應朱倪三人俱各大喜，連聲稱謝。
次日一早，孫仲壽和張朝唐、楊鵬舉等三人告別，說道：「咱們相逢一場，總算有緣。這裏的事只要洩漏半句，後果如何，也不必兄弟多說。」張楊兩人喏喏連聲。孫仲壽每人贈了五十兩銀子的盤費，還派了兩位兄弟送下山去。
張朝唐和楊鵬舉逕赴廣州，途中更無他故。楊鵬舉遭此挫折，心灰意懶，知道江湖上山外有山，人上有人，自己憑這點微末功夫，居然能挨到今日，算得是僥倖之極，此番若非袁承志這小小孩童一言相救，已變成沒眼睛的廢人，想想暗自心驚，當即向鏢局辭了工，便欲回家務農。張朝唐感他救命之恩，見他心情鬱鬱，便邀他同去浡泥國遊覽散心。楊鵬舉眼見左右無事，自己又無家累，當即答允。
三人在廣州僱了海舶，前往浡泥。楊鵬舉住了月餘，見當地太平安樂，真如世外桃源一般，竟然不興歸意，便在張朝唐之父張信的那督府中擔任了一個小小職司。每日當差一兩個時辰，餘下來便是喝酒賭錢，甚是逍遙快樂。
劉芳亮和孫仲壽等說妥結盟之事，眾人在袁崇煥神像前立下重誓，決不相負。劉芳亮正要和袁黨著意結納，聽說崔秋山要教袁承志武藝，甚是歡喜，當下和田見秀先下山去。
袁黨各路好漢，有的去投李自成；有的各歸故鄉，籌備舉事；也有的言明不願造反作亂，只是決不洩露機密，也不和眾兄弟作對為敵。人各有志，旁人也不勉強。
孫仲壽、朱安國、倪浩、應松等留在山上，詳商袁承志日後的出處。
袁承志自崔秋山答應教他伏虎掌後，歡喜得一夜沒睡好覺。翌日大家忙著結盟，沒功夫理會這事。下午眾人紛紛下山，臨行時每人都和幼主作別，又忙碌了半天。
到得晚上，孫仲壽和應松命人點了紅燭，設了交椅，請崔秋山坐在上面，要袁承志行拜師之禮。崔秋山道：「袁家小兄弟我一見就很喜歡，他愛我這套伏虎掌，我就破費幾天功夫，傳授一個大概。但他能不能在這幾天之內學會，學了之後能不能用，可得瞧他的悟性和以後的練習了。這只是朋友之間的切磋，師徒的名份是無論如何談不上的。」應松道：「只要教得一招兩式，就是終身為師。崔大哥何必太謙？」崔秋山一定不肯，大家也只得罷了。
眾人知道武林中的規矩，傳藝時別人不便旁觀，道了勞後，便告辭出來。
崔秋山等眾人出去，正色說道：「承志，這套伏虎掌法，是一位前輩高人傳給我的。我不能盡數領會其中的精奧，功夫也著實還差得遠，但在江湖上對付尋常敵人，也已足夠。他老人家傳授這套掌法之時，曾叫我立誓，學會之後，決不能用來欺壓良善，傷害無辜。」
袁承志一聽，已明其意，當即跪下，說道：「弟子袁承志，學會了伏虎掌法之後，決不敢欺壓良善，傷害無辜，否則，否則……」他不知立誓的規矩，道：「否則就給崔叔叔打死。」
崔秋山一笑，道：「很好。」忽然身子一幌，人已不見。袁承志急轉身時，崔秋山已繞到他的身後，在他肩頭一拍，笑道：「你抓住我。」
袁承志經過朱安國和倪浩、羅大千三位師父的指點，武功也已稍有根基，立即矮身，左手虛幌，右手圈轉，竟不回身，聽風辨形，便向崔秋山腿上抓去。
崔秋山喜道：「這招不錯！」話聲方畢，手掌輕輕在他肩頭一拍，人影又已不見。袁承志凝神靜氣，一對小掌伸了開來，居然也護住了身上各處要害，眼見崔秋山身法奇快，再也抓他不住，當下不再跟他兜圈子捉迷藏，一步一步退向牆壁，突然轉身，靠著牆壁，笑道：「崔叔叔，我見到你啦！」
崔秋山不能再繞到他身後，停住腳步，笑道：「好，好，你很聰明，伏虎掌一定學得成。」於是一招一式的從頭教他。
這路掌法共一百單八式，每式各有三項變化，奇正相生相尅，共三百二十四變。袁承志默默記憶，學了幾遍，已把招式記得大致無誤。崔秋山連比帶說，再把每一招每一變的用法細加傳授。袁承志武功本有根柢，悟性又強，崔秋山一說，便能領會。一個教得起勁，一個學得用心，直至深夜。
第二天一早，崔秋山在山邊散步，見袁承志正在練拳，施展伏虎掌一百單八招的變化，於那勾、撇、捺、劈、撕、打、崩、吐八大要訣，居然也能明其大旨，知其精要。崔秋山很是喜歡，當他練到入神之時突然一躍而前，抬腿向他背心踢去。
袁承志忽聽背後風聲響動，側身避過，回手便拉敵人的右腿，一眼瞥見是崔秋山，急忙縮手，驚叫：「崔叔叔！」崔秋山笑道：「別停手，打下去。」劈面一掌。
袁承志知他是和自己拆招，當下踏上一步，小拳攢擊崔秋山腰胯，正是伏虎掌第八十九招「深入虎穴」。崔秋山讚道：「不錯，就是這樣。」口中指點，手下不停，和他對拆起來，見袁承志出招有誤，便立即糾正。兩人拳來足往，把伏虎掌一百單八式、三百二十四變翻來覆去的拆解。袁承志見這套掌法變化多端，崔秋山運用時愈出愈奇，歡喜無已，用心記憶。拆解良久，崔秋山見他頭上出汗，知道累了，便停住手，要他坐下休息，一面比劃講解。講了一個多時辰，又叫他站起來過招。
兩人自清晨直至深夜，除了吃飯之外，不停的拆練掌法。如此練了七日，到了第八天晚上，崔秋山道：「我所會的已全部傳了給你，日後是否有成，全憑你自己練習了。臨敵之際，局面千變萬化，七分靠功夫，三分靠機靈，一味蠻打，決難取勝。」袁承志點頭受教。
崔秋山道：「明天我就要回到李將軍那裏，今後盼你好好用功。傳我掌法的那位高人曾說，武學高低的關鍵，是在頭腦之中而不在手腳之上，是以多想比多練更加要緊。可惜我的腦筋實在不大靈光，難有甚麼進境，盼你日後練得能勝過了我。」
袁承志和崔秋山相處雖只有八九天，但他把伏虎掌傾囊相授，教之勤，顯見愛之深，聽說明天就要分手，不覺眼眶紅了，便要掉下淚來。崔秋山見他對自己甚是依戀，也不由得感動，輕輕撫摸他頭，說道：「像你這樣聰明資質，武林中實在少見，可惜我們沒機緣長久相聚。」袁承志道：「崔叔叔，我跟你到李將軍那裏。」崔秋山笑道：「你這樣小，那怎麼成？我們跟著李將軍，時時刻刻都在拚命，飽一頓飢一頓的，今天不知明天的事。」
正說話間，忽聽得屋外有野獸一聲怪叫，袁承志奇道：「那是甚麼？不是老虎，也不是狼。」崔秋山道：「是豹子。」靈機一動，道：「咱們去把豹子捉來，我有用處。」袁承志大為興奮，忙問：「甚麼用處？」崔秋山笑而不答，匆匆走了出去。袁承志忙跟出去，見他不帶兵刃，又問：「崔叔叔，你用甚麼兵器打豹子？」
崔秋山不從正門出去，走到內進孫仲壽房外，叫道：「朱大哥、倪大哥都在麼？」朱安國等在房內聚談，聽得叫聲，開門出來。崔秋山笑道：「請各位幫一下手，把外面那頭豹子逼進屋來，我有用處。」倪浩是殺虎能手，連說：「好，好。」拿了獵虎叉，搶先出門。崔秋山叫道：「倪大哥，別傷那畜生。」倪浩遙遙答應，不一會，呼喝聲已起。崔秋山和朱安國、羅大千三人也縱出門去。袁承志拿了短鐵槍想跟出去。孫仲壽道：「承志，別出去，咱們在這裏看。」袁承志無奈，只得和孫仲壽、應松三人憑在窗口觀望。
只見三人拿了火把，分站東西北三方。倪浩使開獵虎叉，在山邊和一頭軀體巨大的金錢豹正自翻翻滾滾的拚鬥。他一柄叉護住全身，不讓豹子撲近，卻也不出叉戳刺。豹子見到火光，驚恐想逃，卻被朱、崔、羅三人阻住了去路。豹子見崔秋山手中沒兵器，大吼著向他撲來。崔秋山閃身避開利爪，右掌在豹子額頭一擊，豹子登時翻了個觔斗，轉身向南。南面房門大開，豹子不肯進屋，東西亂竄，但給眾人逼住了，無路可走。崔秋山縱身而上，在豹子後臀上猛力一腳。豹子負痛，吼叫一聲，直竄進屋去。
那時應松已把各處門戶緊閉，僅留出西邊偏殿的門戶。豹子見兩人手持火把追來，東爬西搔，胡胡吼叫，奔進西殿。羅大千隨後把門關上，一頭大豹已關在殿內。
眾人都很高興，望著崔秋山，不知他要豹何用。崔秋山笑道：「承志，你進去打豹！」此言一出，眾人都吃了一驚。孫仲壽道：「這怕不大妥當吧？」崔秋山道：「我在旁邊瞧著，這畜生傷不了他。」袁承志道：「好！」挺了短槍，就去開門。崔秋山道：「放下槍，空手進去！」
袁承志一怔，隨即會意是要他以剛學會的伏虎掌打豹，不禁膽怯。崔秋山道：「你害怕了麼？」袁承志更不遲疑，拔開殿門上的插頭，推門進去，只聽「胡」的一聲巨吼，一團黑影迎面撲來。他右腿一挫，讓開來勢，反手出掌，打在豹子耳上，使的正是伏虎掌法中的「羅漢傳經」。這掌雖然打中，可是手小無力，豹子不以為意，回頭便咬。袁承志竄到豹子背後，拉住豹尾一扯。
這時崔秋山已站在一旁衞護，惟恐豹子猛惡，袁承志制牠不住，但見他一路伏虎掌已使得頗為純熟，豹子三撲三抓，始終沒碰到他一點衣角，反中了他一掌一腳，心下暗暗歡喜。
孫仲壽等見袁承志空手鬥豹，雖說崔秋山在一旁照料，畢竟關心，各人拿了火把，站在殿角旁觀。朱安國和倪浩手扣暗器，以便緊急時射豹救人。火光中袁承志騰挪起伏，身法靈活，初時還東逃西竄，不敢和豹子接近，後來見所學掌法施展開來妙用甚多，閃避攻擊，得心應手，不由得越打越有精神。
他見手掌打上豹身毫無用處，突然變招，改打為拉，每一掌擊到，回手便扯下一把毛來。豹子受痛，吼叫連連，對他的小掌也有了忌憚，見他手掌伸過來時，不住吼叫退避，露齒抵抗。但袁承志手法極快，豹子總是閃避不及，一時殿中豹毛四處飛揚，一頭好好的金錢豹子，被他東一塊西一塊的扯去了不少錦毛。眾人都笑了起來。
豹毛雖被抓去，但空手終究制牠不住，酣鬥中他突使一招「菩薩低眉」，矮身正面向豹子衝去。豹子受驚，退了一步，隨即飛身撲來，一剎那間，袁承志已在豹子腹下。
倪浩大驚，雙鏢飛出。那豹伸右腳撥落雙鏢。這時袁承志卻已不見。眾人凝目看時，只見他躲在豹子腹底，一雙腿勾住了豹背，腦袋頂住了豹了的下頦，叫牠咬不著抓不到。豹子猛跳猛竄，在地下打滾，袁承志始終不放。他知時間一久，自己力氣不足，只要一鬆手腳，不免傷在豹子爪下，忙叫：「崔叔叔，快來！」
崔秋山道：「取牠眼睛！」一言提醒，袁承志右臂穿出，兩根手指插向豹子右眼，豹子痛得狂叫，竄跳更猛。崔秋山踏上一步，蓬蓬連環兩掌，把豹子打得頭昏腦脹，翻倒在地，隨即一把抱起袁承志，笑道：「不壞，不壞，真難為你了。」
孫仲壽等人俱已驚得滿頭大汗，均想：「崔秋山為人雖然不錯，但在李闖手下，整日價幹的盡是亡命生涯，大膽妄為。他不知袁公子這條命可有多尊貴。」又想：「袁公子經他教了八天，武藝果然大有長進。」崔秋山打開殿門，在豹子後臀上踢了一腳，笑道：「放你走吧！」那豹子直竄出去，忽然外面有人驚叫起來。
眾人只道豹子奔到外面傷了人，忙出去看時，這一驚非同小可。只見滿山都是點點火光，火光照耀下刀槍閃閃發亮，原來官兵大集，圍攻聖峯嶂來了。看這聲勢，要脫逃實非容易。在山下守望的黨人想來均已被害，是以事前毫無警報，而敵兵突然來臨。
孫仲壽等都是身經百戰，雖然心驚，卻不慌亂，均想：「可惜山上的弟兄都已散去了，否則當年在寧遠大戰，十幾萬韃子精兵，也給我們打得落荒而逃，又怎怕你們這些廣東官兵？」其時遼東兵精，甲於天下，袁崇煥的舊部向來不把南方官兵放在眼裏。
孫仲壽當即發令：「羅將軍，你率領煑飯、打掃、守祠的眾兄弟到東邊山頭放火吶喊，作為疑兵。」羅大千應令去了。孫仲壽又道：「朱將軍、倪將軍，你們兩位到前山去，每人各射十箭，教官兵不敢過份逼近，射後立刻回來。」朱倪二人應令去了。
孫仲壽道：「崔大哥，有一件重任要交託給你。」崔秋山道：「要我保護承志？」孫仲壽道：「正是。」說著和應松兩人拜了下去。崔秋山吃了一驚，連忙還禮，說道：「兩位有何吩咐，自當遵從，快休如此。」
只聽得喊聲大作，又隱隱有金鼓之聲，聽聲音是山上發出，原來羅大千已把祠中的大鼓大鐘抬出來狂敲猛打，擾亂敵兵。孫仲壽道：「袁督師只有這點骨血，請崔大哥護送他脫險。」崔秋山道：「我必盡力。」
這時朱安國和倪浩已射完箭回來。孫仲壽道：「我和朱將軍一路，會齊羅將軍後，從東邊衝下，應先生和倪將軍一路，從西邊衝下。我們先衝，把敵兵主力引住。崔大哥和承志再從後山衝下，大家日後在李闖將軍那裏會齊。」眾人齊聲答應。
袁承志得應松等數載教養，這時分別，心下難過，跪下去拜了幾拜，說道：「孫叔叔、應叔叔、朱叔叔、倪叔叔、我，我……」喉中哽住了說不下去。孫仲壽道：「你跟著崔叔叔去，要好好聽他的話。」袁承志點頭答應。
只聽得山腰裏官兵發喊，向山上衝來，應松道：「我們走吧。崔大哥，你稍待片刻再走。」眾人各舉兵刃，向下衝去。倪浩見崔秋山沒帶兵器，把虎叉向他擲去，說道：「崔大哥，接住。」
崔秋山道：「還是倪兄自己用吧！」接住虎叉想擲還給他。倪浩已去得遠了，於是右手持叉，左手拉著袁承志向山後走去。只見後山山坡上也滿是火把，密密層層的不知有多少官兵。山下箭如飛蝗，亂射上來，崔秋山於是退回祠中，跑到厨下，揭了兩個鍋蓋，一大一小，自己拿了大的，把小鍋蓋遞給袁承志，說道：「這是盾牌，走吧！」兩人展開輕身功夫，向黑暗中竄去。
不一會，官兵已發現兩人蹤迹，吶喊聲中追了過來，數十枝箭同時射到。
崔秋山擋在袁承志身後，揮動鍋蓋，一一擋開來箭，只聽得登登登之聲不絕，許多箭枝都射在鍋蓋之上。兩人直闖下山去。眾官兵上來攔阻，崔秋山使開獵虎叉，叉刺桿打，霎時間傷了十多名官兵，袁承志的短鐵槍雖然難以傷人，卻也儘可護身。官兵見是個幼童，也不怎麼理會他。片刻間兩人已奔到山腰。
剛喘得一口氣，忽然喊聲大作，一股官兵斜刺裏衝到，當先一名千戶手持大刀，惡狠狠的砍來。崔秋山舉叉一架，覺他膂力頗大，一叉「毒龍出洞」，直刺過去。那千戶舉刀格開，叫道：「弟兄們上啊！」崔秋山不願戀戰，舉起鍋蓋向那千戶面前一幌。那千戶向右閃避，崔秋山大喝一聲，手起叉落，從他脅下插了進去，待拔出叉來，轉頭卻不見了袁承志，心中大驚，只見左邊一羣人圍著吆喝。
他大踏步趕過去，挺叉亂戳，官兵紛紛閃避，奔到近處，果見袁承志給圍在垓心，手中短鐵槍已被打落，正展開伏虎掌法和三名官兵對敵，畢竟年幼力弱，掌法又是初學未熟，左支右絀，情勢危急。崔秋山更不打話，刷刷兩叉，刺倒兩名官兵，左手拉了袁承志便走。官兵大叫追來，崔秋山斗然回頭，刷刷兩叉，刺倒了追得最近的兩名官兵，再踏上一步，叉桿抄起，把一名官兵挑了起來，直摜在山石之上。那兵慘叫一聲，立時跌死。
眾官兵見他如此勇悍，嚇得止步不追。崔秋山把袁承志挾在脅下，展開輕功提縱術，直向黑暗無人處竄去，不一會便和眾官兵離得遠了。
崔秋山放下袁承志，問道：「沒受傷吧？」袁承志舉手往臉上抹汗，只覺黏膩膩的，月光下一看，滿手是血，看崔秋山時，臉上、手上、衣上，盡是血迹斑斑，說道：「崔叔叔，血……血……」崔秋山道：「不要緊，是敵人的血，你身上有那裏痛麼？」袁承志道：「沒有。」崔秋山道：「好，咱們再走！」
兩人矮了身子，在樹叢中向下鑽行，走了小半個時辰，樹叢將完，崔秋山探頭一望，見山下火把明亮，數百名官兵守著，悄聲道：「不能下去，後退。」兩人回身走了數百步，見有一個山洞，洞前生著一排矮樹，便鑽進洞去。
袁承志畢竟年幼，雖然身在險地，但疲累之餘，躺下不久便睡著了。崔秋山把他輕輕抱起，倚在自己懷裏，側耳靜聽。只聽呼喊之聲連續不斷，過了一會，眼見山頂黑烟冒起，紅光沖天，想是袁崇煥的祠堂已給官兵燒了。又過了半個多時辰，聽得山上吹起號角，崔秋山跟官兵大小打過數十仗，知是收隊下山的號令。不一會，大隊人馬聲經身旁過去，絡繹不絕，原來這山洞就在官兵下山道路之旁。
再過一會，忽聽外面樹叢中有人坐了下來，崔秋山右手提起鋼叉，左手放在袁承志嘴邊，防他在夢中發出聲響，凝神靜聽。只聽一人喝道：「那姓袁的逆賊留下一個兒子，到那裏去了？」這句話聲音很響，登時把袁承志吵醒。崔秋山左手輕輕按住他嘴。
聽得那人喝道：「你說不說？不說我先砍斷你一條腿。」一個聲音罵道：「你砍就砍！我們在邊庭上一刀一槍打韃子，豈來怕你？」聽口音正是應松的聲音。袁承志悄聲道：「應叔叔！」那人又罵：「你真的不說？」應松呸的一聲，似乎一口唾沫吐向他的臉上，，接著一聲慘叫，似乎已被他一刀砍傷。
袁承志再也忍耐不住，用力一掙，掙脫了崔秋山拉住他的手，大叫一聲：「應叔叔！」直竄出去。火光中見一人正提刀向摔跌在地的應松砍落，他和身縱上，施展伏虎掌中的「左擊右擒」之法，一拳正中那人右眼。那人只覺眼中金星直冒，手腕一痛，一柄刀已被奪去。袁承志順手一刀，砍在他肩頭，雖然力弱，沒把一條肩膀卸下，也已痛得他怪聲大叫。
眾官兵出其不意，都吃了一驚，登時逃散，待得看清楚只是一個幼童，當即回轉身來，刀槍齊下，眼見就要把他砍成碎塊。
突然火光中一柄鋼叉飛出，各官兵只覺虎口劇震，兵刃紛紛離手。崔秋山一把抓住袁承志後心，直縱出去。眾官兵放箭時，兩人早已直奔下山。
崔秋山這一露形，奉太監曹化淳之命前來搜捕的東廠番子之中，便有四名好手跟蹤下來。但見他脅下挾著一個幼童，但仍是縱跳如飛，迅捷異常，一名番子取出一枝甩手箭，使足手勁，擲了出去。
崔秋山聽得腦後生風，立即矮身，那枝箭從頭頂飛了過去，就這麼停得一停，另一人已扣住三枝鋼鏢，連珠發出。崔秋山把袁承志往地下一放，左手一抄，接住兩枝鋼鏢，避開了第三枝，正待發回，敵人的袖箭、飛蝗石已紛紛打來。崔秋山手接叉撥，閃避暗器，拉著袁承志向山下逃去。
這時他們離官兵大隊已遠，可是四名番子始終緊追不捨。其中一人大叫道：「相好的，你撇下兵器，乖乖的跟老子回去，就讓你少吃些苦頭。」崔秋山暗暗把鋼鏢交到右手，待他追近，突然兩鏢一上一下，疾如閃電般射了出去。那人「啊喲」一聲，腿上一鏢早著，登時栽倒。其餘三人畧一停頓，又分頭掩來。
崔秋山見敵人追近，對袁承志說：「我去奪那人的刀來給你。」把虎叉往地下一插，反奔迎敵。那使雙刀的一招「雲龍三現」，刷刷刷連環三招，崔秋山竟搶不入去，另一個使鐵鞭的卻已欺近袁承志身旁。
崔秋山見一時奪不下敵刃，而那邊袁承志卻已危急，驀地回身，滴溜溜一個旋身，已欺到那使鐵鞭的人背後，一招「金龍探爪」，五指向他後心抓去。那人鐵鞭正向袁承志後心掃去，忽覺身後來了敵人，單鞭一立，轉過身來。崔秋山以快打慢，出手迅捷異常，那人招架不住，只得連連倒退。袁承志忽地踏步上前，飛起一腿，踢中了他後臀。那人怒吼一聲，橫鞭反擊，突覺掌心一震，鞭梢已被崔秋山抓住。就在這時，那使雙刀的與使鬼頭刀的三件兵刃同時向崔秋山背後打來，這時腿上中鏢那人也已爬起，挺槍向袁承志左脅刺去。
此時危機四伏，好個崔秋山，在這間不容髮的緊急關頭，竟然於輕重緩急料得絲毫無誤，吭聲吐氣，嘿的一聲，右掌一招「降龍伏虎」，正打在那使鐵鞭的人胸口。這一招是伏虎掌中三大絕招之一，那人如何抵擋得住，全身騰空，向那腿上中鏢的人槍尖上仰跌下去。幸得那人急忙縮槍，這才騰的一聲，跌在地下，沒給槍尖穿個透明窟窿。崔秋山單鞭奪到，反掄過來，噹的一聲，將三把刀同時架開，縱過去拉了袁承志向山下竄去。
四名番子見崔秋山霎時之間奪鞭使掌，同時拆開了四人的進襲，武功精強，不敢再追，站定身子，紛紛發出暗器。
崔秋山黑暗之中聽得颼颼之聲不絕，忙把袁承志拉在胸前，竄高躍低的閃避，但畢竟手中抱了人，縱跳不便，避開了右邊打來的三枚菩提子，只覺左腿一痛，已中了暗器。傷處剛剛痛過，立即發癢，心中大驚，知道箭上有毒，不敢停留，急向山下奔逃，但這一來，毒發更快，再跑得幾步，左腿一陣麻痺，一個踉蹌，跌倒在地。袁承志大驚，急叫：「崔叔叔。」四名番子見他跌倒，高呼大叫，隨後趕來。
崔秋山道：「承志，快走，快走，我擋住他們。」袁承志雙掌一錯，躍到崔秋山身後，預備迎敵。崔秋山心想：「憑你這點功夫，居然想保護我。」但心中也自感動。
轉眼之間，敵人已經追到，兩個使刀的奔在最前。使鬼頭刀的人想生擒活捉，翻轉刀背，向袁承志足踝上擊來。袁承志一躍避過。
崔秋山撐起右腿，半跪在地，手中鐵鞭筆直的向使雙刀的擲去。那人待要避讓，已然不及，鐵鞭從他額頭上插了進去。使鬼頭刀的人一呆，崔秋山和身撲上，十指緊緊鉗住他喉嚨，那人揮刀向崔秋山臂上砍來，崔秋山手上加勁，那人這一刀雖然砍中，卻已無力，片刻間便即氣絕而死。其餘兩人本已受傷，又見敵人如此兇悍，嚇得魂飛魄散，那裏還敢來追，連忙逃回。崔秋山臂上流血，幸好傷勢不重，但左腿已全無知覺。
他咬緊牙關，拾起刀撐在地下，左手握住，站了起來。這時敵人雖已逃走，但不久定然召援再來，當地決計不能多留，只得左腿虛懸，向山下走去。袁承志站在他右邊，讓他右手搭在自己肩上，一蹺一拐的向前趕路。
走了一陣，崔秋山左腿毒性向上延伸，牽動左手也漸漸無力，只得以右手支撐。袁承志只覺肩頭越來越重，但他一聲不哼，奮力扶持著崔秋山前行。
又走一陣，兩人實已筋疲力盡。袁承志忽見山邊有間農舍，說道：「崔叔叔，前面有人家，咱們進去躲一躲。你再熬一下吧！」崔秋山點點頭，勉力拖著半邊身子向前挨去，到得門邊，全身脫力，摔倒在地。
袁承志大驚，俯身連叫：「崔叔叔！」那農舍的門呀的一聲開了，出來一個中年婦人。袁承志道：「大娘，我們遇到官兵。我叔叔受了傷，求求你讓我們借宿一晚。」
那農婦叫出一個十六七歲的少年來，命他幫著把崔秋山扶進去，拼起三條長凳，讓他躺下。崔秋山中毒甚深，虧得武功精湛，心智倒沒昏亂，叫袁承志把油燈移近左腿處察看。兩人都嚇了一跳，原來那左腿已腫大了幾乎一半，紫中帶黑，十分怕人。
崔秋山請那農家少年裹好他臂上傷口，再用布條在他左腿腿根處用力纏緊，以防毒氣攻心，然後抓住箭羽，拔了出來，跟著流出來的都是黑血。崔秋山俯身要去吮吸毒血，但腿子腫大，嘴巴夠不到。袁承志俯下身去，把傷口中的黑血一口口的吸了出來，吐在地下，吸了三四十口之後，血色才漸漸變紅。崔秋山歎了一口氣道：「這毒藥總算還不是最厲害的那種。你快漱口。」那農婦在旁瞧著，不住唸佛。
次日午後，那少年報說官兵已經退盡。崔秋山腿腫漸消，但全身發燒，胡言亂語起來。袁承志沒了主意，只是急得要哭。
那農婦道：「這件小官，我瞧你叔叔的毒氣還沒去盡，總得到鎮上請大夫瞧瞧才好。」袁承志道：「是，是，可是怎麼去？」那農婦心腸甚好，借了一輛牛車，命少年送了他們到鎮上。
那少年把他們送入客店之後，逕自去了。崔袁兩人出來時身上都沒帶錢，袁承志不知如何是好，望著床上昏迷不醒的崔秋山發愁。店伴來問吃甚麼東西，袁承志答不上來，只好推說不餓，一個人坐著想哭。
過了良久，崔秋山終於醒來，袁承志忙問他怎麼辦。崔秋山道：「你身上帶著甚麼值錢的東西沒有？」袁承志道：「這項圈成嗎？」說著從衣內貼肉處除了下來。崔秋山一看，見項圈是金的，鑲著八顆小珍珠，項圈鎻片上刻著「富貴恆昌」四個大字，還有兩行小字，一行是「袁公子承志週歲之慶」，一行是「小將趙率教敬贈」，才知道是袁承志做週歲時，他父親部下大將趙率教所贈。
趙率教和祖大壽、何可綱、滿桂三人是袁崇煥部下的四大名將。當年寧錦大捷，趙率教部殺傷清兵甚眾，官封左都督、平遼將軍。崇禎二年十月，清兵繞過山海關，由大安口入寇京師，袁崇煥率四將千里回援，反為崇禎見疑而下獄。趙率教和滿桂出戰，先後陣亡。祖大壽與何可綱憤而率部自行離去，後來袁崇煥在獄中寫信去勸，祖何二將才再歸朝。
趙率教是袁崇煥部下名將，天下知聞，但這時崔秋山迷迷糊糊，未能細想，便道：「叫店伴陪你到當舖去，把項圈當了吧，將來咱們再來贖回。」袁承志說：「好，我就去。」於是請店伴同去鎮上的當舖。
當舖朝奉拿到項圈，一看之下，吃了一驚，問道：「小朋友，這項圈你從那裏來的？」袁承志道：「是我自己的。」那朝奉臉色登時變了，向袁承志上上下下打量良久，說道：「你等一下。」拿了項圈到裏面去，半天不出來。袁承志和那店伴等的著急，又過了好一會，那朝奉才出來，說道：「當二十兩。」袁承志也不懂規矩，還是那店伴代他多爭了二兩銀子。袁承志拿了銀子和當票，順道要店伴陪去請了大夫，這才回店，那知身後已暗暗跟了兩名公差。
袁承志回到店房，見崔秋山已沉沉睡熟，額上仍然火燙，大夫還沒到來。他心中焦急，走到店門外面張望，忽見七八名公差手持鐵鍊鐵尺，搶進店來。一人說道：「就是這孩子！」為首的公差喝道：「喂，孩子，你姓袁嗎？」
袁承志嚇了一跳，道：「我不是。」那公差哈哈一笑，從懷中掏出那個金項圈來，說道：「這項圈你從那裏偷來的？」袁承志急道：「不是偷來的，是我自己的。」那公差笑道：「袁崇煥是你甚麼人？」
袁承志不敢回答，奔進店房，猛力去推崔秋山，只聽得外面公差喊了起來：「聖峯嶂的奸黨躲在這裏，莫讓逃了。」崔秋山霍地坐起，要待掙下地來，卻那裏能夠？腳剛著地，便即跌倒。
這時眾公差已湧到店房門口，袁承志不及去扶崔秋山，縱出門來，雙掌一錯，擋在門口，當時心中只有一個念頭：「決不能讓他們捉了崔叔叔去。」
門外是個大院子，客店中夥計客人聽說捉拿犯人，都擁到院子裏來瞧熱鬧，見七八名公差對著一個十歲左右的孩子發威，均覺奇怪。
只見一名公差抖動鐵鍊，往袁承志頭上套去。袁承志退後一步，仍是攔在門外，不讓公差進門。那公差抖鐵鍊套人，本是吃了十多年衙門飯的拿手本事，豈知一個小小孩子居然身手敏捷，這一下竟沒套住，老羞成怒，伸右手來揪他頭上的小辮子。
袁承志見這許多公差聲勢洶洶，本已嚇得要哭，但見對方伸手抓到，頭一偏，使出伏虎掌法中的「橫拖單鞭」，在他手腕上一拉。那公差一個踉蹌，險些跌倒，怒火更熾，飛腿猛踢，罵道：「小雜種，老子今日要你好看。」
袁承志蹲下身來，雙手在他大腿和臀部一托，借力乘勢，向外推送，那公差肥肥一個身軀登時凌空飛了出去，砰的一聲，結結實實的跌在地下。袁承志本來也沒這麼大氣力，全是乘著那公差一踢之勢，斜引旁轉，把他狠狠摔了一交。這一招仍是伏虎掌法。
旁觀眾人齊齊轟然叫好。他們本來憤恨大人欺侮小孩，何況官府公差橫行霸道，素為眾百姓所側目切齒，這時眼見公差反而落敗，而且敗得如此狼狽，不由得大聲喝釆。
其餘的公差也都一楞，暗想這孩子倒有點邪門，互使眼色，手舉單刀鐵尺，一湧而上。旁觀眾人見他們動了傢伙，俱都害怕，紛紛退避。袁承志雖學了數年武藝，究竟年幼，又敵不過對方人多，無可奈何之中，只有奮力抵擋。不久肩頭便吃鐵尺重重打中了一下，忍不住便要哭出聲來。
正在危急之際，忽然左邊廂房中奔出一條大漢，飛身縱起，落在袁承志面前，伸出雙手亂抓亂拿，也不知他用了甚麼手法，頃刻之間，已把眾公差的兵刃全都奪下。幾名公差退得稍遲，被他幾拳打得眼青口腫。這大漢啊啊大叫，聲音古怪。
一名公差喝道：「我們捉拿要犯，你是甚麼人？快快滾開。」那大漢全不理會，身子一幌，已欺到他身前，右手抓住他胸口，往外擲出。那公差猶如斷線鳶子一般，悠悠幌幌的飛出牆外，砰蓬一聲，摔得半死。其餘的公差再也不敢停留，一鬨出外。
那大漢走到袁承志跟前，雙手比劃，口中啞啞作聲，原來是個啞巴，似在問他來歷。袁承志不知如何告訴他才好，甚是焦急。
那大漢忽然左掌向上，右掌向地，從伏虎掌的起手式開始，練了起來，打到第十招「避撲擊虛」就收了手。袁承志會意，從第十一招「橫踹虎腰」起始，接下去練了四招。那啞巴一笑，點點頭，伸臂將他抱起，神態甚是親熱。
袁承志指指店房，示意裏面有人。那啞巴抱著他進房，只見崔秋山坐在地下，臉色猶如死灰，吃了一驚，放下袁承志，走上前去。崔秋山卻認得他，做做手勢，指指自己的腿。
那啞巴點點頭，左手牽著袁承志，右手抱起了崔秋山，大踏步走出客店。崔秋山是一百幾十斤重的一條大漢，但啞巴如抱小孩，毫不費力，步履如飛的出去。
兩名公差躲在一旁，見那啞巴向西走去，遠遠跟在後面，想是要知道他落腳之所，再邀人大舉拿捕。
這時崔秋山又昏了過去，人事不知。啞巴聽不到身後聲息，袁承志拉拉啞巴的手，嘴巴向後一努。啞巴回過頭來，瞧見了公差，卻似視而不見，繼續前行。
走出兩三里路，四下荒僻無人，啞巴忽地把崔秋山往地上一放，縱身躍到那兩名公差面前。兩公差轉身想逃，那裏來得及，早被他一手一個，揪住後心，直向山谷中摔了下去，兩聲慘叫，都跌得腦漿迸裂而死。
啞巴摔死公差，抱起崔秋山，健步如飛的向前疾走。這一來袁承志可跟不上了，他雖勉力對付，兩條小腿拚命搬動，但只跑了里許，已氣喘連連。啞巴一笑，俯身把他抱在手中，他雙手分抱兩人，反而跑得更快，跑了一會，折而向左，朝山上奔去。
翻過兩個山頭，只見山腰中有三間茅屋，啞巴逕向茅屋跑去。快要到時，屋前一人迎了過來，走到臨近，原來是個二十多歲的少婦。她向啞巴點了點頭，見到崔袁兩人，似感訝異，和啞巴打了幾個手勢，領著他們進屋。
那少婦叫道：「小慧，快拿茶壺茶碗來。」一個女孩的聲音在隔房應了一聲，提了一把粗茶壺和幾隻碗過來，怔怔的望著崔袁兩人，一對圓圓的眼珠骨溜溜的轉動，甚是靈活。
袁承志見那少婦粗衣布裙，但皮色白潤，面目姣好，那女孩也生得甚是靈秀。
那少婦向袁承志道：「這孩子，你叫甚麼名字？怎麼遇上他的？」袁承志知她是啞巴的朋友，於是毫不隱瞞的簡畧說了。
那少婦聽得崔秋山中毒受傷，忙拿出藥箱，從瓶中倒出些白色和紅色的藥粉，混在一起，調了水給崔秋山喝了，又取出一把小刀，將他腿上腐肉刮去，敷上些黃色的藥末，過了一陣，用清水洗去，再敷藥末。這般敷洗了三次，崔秋山哼出聲來。那少婦向袁承志一笑，說道：「不妨事了。」打手勢叫啞巴把崔秋山抱入內堂休息。
那少婦收拾藥箱，對袁承志道：「我姓安，你叫我安嬸嬸好啦。這是我女兒，她叫小慧，你就躭在我這裏。」袁承志點點頭。安大娘隨即下厨做麵。袁承志吃過後，疲累了一天一夜，再也支持不住，便伏在桌上睡著了。
次晨醒來時發覺已睡在床上。小慧帶他去洗臉。袁承志道：「我去瞧瞧崔叔叔，他傷勢好些麼？」小慧道：「啞巴伯伯早揹了他去啦！」袁承志驚道：「當真？」小慧點點頭。袁承志奔到內室，果然不見崔秋山和啞巴的蹤影。他茫然無主，哇的一聲哭了出來。小慧忙道：「別哭，別哭！」袁承志那裏肯聽？小慧叫道：「媽媽，媽媽，妳快來！」安大娘聞聲趕來。小慧道：「他見崔叔叔他們走了，哭起來啦！」
安大娘柔聲說道：「好孩子，你崔叔叔受了傷，很厲害，是不是？」袁承志點點頭。安大娘又道：「我只能暫行救他，讓他傷口的毒氣不行開來。不過時候隔得太久啦，只怕他腿要殘廢，因此啞巴伯伯揹他去請另外一個人醫治。等他醫好之後，就會來瞧你的。」袁承志慢慢止了哭泣。安大娘道：「他就會好的。快洗臉，洗了臉咱們吃飯。」
吃過早飯後，安大娘要他把過去的事再詳詳細細說一遍，聽得不住歎息。就這樣，袁承志便在安大娘家中住了下來。
安大娘叫他把所學武功練了一遍，看後點點頭說：「也真難為你了。」此後安大娘每日叫他自行練武，練得好不好，卻從不加指點，在他練的時候也極少在旁觀看。小慧本來常和他在一起，在他練武之時，卻總被媽媽叫了開去。
袁承志從小沒了父母，應松、朱安國等人雖然對他照顧周到，但這些叱吒風雲的大將，照料孩子總不如何在行。現下安大娘對他如慈母般照顧，親切周到，又有小慧作伴，這時候所過的，可說是他生平最溫馨的日子了。
如此過了十多天，這一日安大娘到鎮上去買油鹽等物，還預備剪幾尺布來，給袁承志縫一套衫褲。那日他在聖峯嶂遇難，連滾帶爬，衣服已給山石樹枝撕得破爛。安大娘雖早給他縫補好了，但滿身補釘，總不好看。安大娘叮囑兩個孩子在家裏玩，別去山裏，怕遇上狼。兩個孩子答應了。
安大娘走後，兩個孩子果然聽話不出，在屋裏講了幾個故事，又捉了半天迷藏，後來拿些小碗小筷，假裝煑飯。小慧道：「你在這裏殺雞，我去買肉。」所謂殺雞，是把蘿蔔切成一塊一塊，而買肉則是在門口撿野栗子。
小慧去了一會，好久不見回來，袁承志大叫：「小慧，小慧。」不見答應，想起安大娘的話，怕真遇上了狼，忙在灶下拿了一根火叉，衝出門去。
剛走出大門，一驚非同小可，只見小慧被一條身穿武官服色的大漢挾在脅下，正要下山。袁承志大喊一聲，挺叉向那大漢背後刺去。大漢猝不及防，總算袁承志人矮，沒刺到背心，臀部卻已重重的吃了一叉，只是火叉頭鈍，刺不入肉。大漢大怒，放下小慧，拔出單刀，轉身刷的就是一刀。袁承志曾跟倪浩學過槍法，將一柄火叉照著「岳家神槍」槍法使了開來，竟然有攻有守，和那大漢對打起來。
那大漢力大刀勁。袁承志仗著身法靈便，居然也對付著拆了十來招。那大漢見戰不下一個小孩，心中焦躁，雙腿一蹲，刀法忽變。那大漢起初出招，倒有一大半都砍空了，只因袁承志身矮，大漢砍向敵人上部的刀法，全都砍在空中，他覺察之後，便改使地堂刀法，只是覺得對付一個小小孩童，不必小題大做，是以並不躺下地來。
這一來袁承志登感吃力，正危急間，忽見安小慧拿了一柄長劍，一劍「仙人指路」，向大漢身上刺去。大漢罵道：「呸！你這小妞也來找死。」單刀橫砍過去。他不欲傷她，只想震去她手中長劍。
那知小慧身手靈活，長劍忽地圈轉，挽了個平花，一招「三寶蓮台」，回刺大漢後胯，同時袁承志的火叉也是一招「毒龍出洞」刺將過去。那大漢一時之間竟給兩個小孩鬧了個手忙腳亂。
袁承志起初見小慧過來幫手，擔心她受傷，但三招兩式之後，見她身手便捷，居然一手「達摩劍法」使得也頗純熟，他小孩好勝，不甘落後，一柄火叉使得更加緊了。
那大漢見兩個小孩的槍法和劍法竟然都是頭頭是道，然而力氣太小，總歸無用，於是封緊門戶，又笑又罵的一味游鬥。耗了一陣，兩個小孩果然支持不來了。
那大漢提起單刀，對準小慧長劍猛力劈去，小慧避讓不及，長劍和單刀一碰，拿揑不住，登時脫手向天空飛去。袁承志大駭，火叉「舉火撩天」，在大漢面前一幌。大漢舉刀架開，飛腳把小慧踢倒。袁承志不顧性命的舉叉力攻，但心中慌亂，火叉已使得不成章法。
大漢哈哈大笑，搶上一步，揮刀向他當頭砍下。袁承志橫叉招架，大漢左手已拉住叉頭，用力一扭。袁承志只覺虎口劇痛，火叉脫手。那大漢不去理他，隨手把火叉擲在地下，奔到小慧身旁，右手抄出，已抱住她腰，向前奔去。
袁承志手上雖痛，但見小慧被擒，拾起火叉隨後趕來。大漢罵道：「你這小鬼，不要性命了？」左手抱住小慧，右手挺刀回身便砍，拆得五六招，袁承志左肩被單刀削去一片衣服，皮肉也已受傷，鮮血直冒。大漢笑道：「小鬼，你還敢來麼？」
那知袁承志竟不畏縮，叫道：「你放下小慧，我就不追你。」拿了火叉，仍是緊追不捨。那大漢怒從心起，惡念頓生，想道：「今日不結果這小鬼，看來他要糾纏不休。」大喝一聲，回身挺刀狠砍，數合拆過，腳下一勾，已把袁承志絆倒，再不容情，舉刀砍落。
小慧大驚，雙手拉住大漢手臂，狠狠在他手腕上咬了一口。大漢吃痛，哇哇怒吼，袁承志乘機滾了開去。大漢反手打了小慧一個耳括子，又舉刀向袁承志砍下。袁承志側身急避，被他刀尖在額上帶過，左眉上登時劃了一道口子，鮮血直流。
大漢料想他再也不敢追來，提了小慧就走。那知袁承志猶如瘋了一般，緊緊抱住大漢左腳，百忙中還使出伏虎掌法，一個「倒扭金鐘」，將他左腿扭轉。要知袁崇煥是廣東東莞人，袁承志血中秉承著廣東人那股寧死不屈的倔強性子，雖然情勢危急，仍是不讓小慧給敵人擒去。
那大漢又痛又氣，右腿起處，把他踢了個觔斗，舉刀正要砍下，忽聽背後有人喝斥，跟著後腦上咚的一聲，一陣疼痛，後頸中跟著濕淋淋、黏膩膩地，不知是不是給人打得後腦杓子流血，心下驚惶，回過頭來，只見安大娘雙手揚起，站在數丈之外。
那大漢知她厲害，捨了袁承志，抱住小慧要走。安大娘右手連揚，三枚雞蛋接連向他面門打去。大漢東躲西閃，避開了兩枚，第三枚再也閃避不開，撲的一聲，正中鼻樑，滿臉子都是蛋黃蛋白。安大娘從籃中一掏，摸到最後一枚雞蛋，又是一下打在他左目之上。她手勁不弱，雖是一枚雞蛋，可也已打得他頭暈眼花。
那大漢罵道：「他奶奶的，你不炒雞蛋請老子吃，卻用雞蛋打老子！」拋下小慧，左手在眼上抹了幾下，舉刀向安大娘殺來。安大娘手中沒兵刃，只得連連閃避。
袁承志見她危急，挺叉又向大漢後心刺去，這時他見來了幫手，精神大振，一柄火叉挑刺遮攔，「岳家神槍」的槍法使得似模似樣。
安大娘緩出了手，靈機一動，把買來給袁承志做衣服的一疋布從籃中取了出來，迎風抖開，拋入身後的小溪，跟著撿起三塊石子向大漢打去。大漢旣要閃避石子，又要招架袁承志的火叉，連退了三步。
安大娘拿起浸濕的布疋，喝道：「胡老三，你乘我不在家，上門來欺侮小孩子，算是那一門子的好漢？」呼喝聲中，一疋布已向大漢迎面打去。她的內力雖還不足以當真「束濕成棍」，把一疋布當作棍子使，但長布浸水，揮出來卻也頗有力道。胡老三皺起眉頭，抬腿把袁承志踹倒，與安大娘鬥了起來。
安大娘的武功本就在胡老三之上，此時心中憤恨，一疋濕布揮出來更是有力。胡老三背上連被布端打中兩下，水珠四濺，只覺背心隱隱發痛，出手稍慢，單刀突被濕布裹住。安大娘用力迴扯，胡老三單刀脫手。
他縱出兩步，獰笑道：「我是受你丈夫之託，來接他女兒回去。陰魂不散，總有一天再找上你。小潑婦，我們錦衣衞的人你也敢得罪，當真不怕王法麼？」安大娘秀眉直豎，將濕布橫掃過去。胡老三早防到她這著，話剛說完，已轉身躍出，遠遠的戟指罵道：「他媽的，今天你請我吃生雞蛋，老子下次捉了你關入天牢，請你屁股吃笋炒肉，十根竹籤插進你的指甲縫，那時你才知道滋味！今日瞧在你老公份上，且饒你一遭。」罵了幾句，向山下疾奔而去。安大娘也不追趕，回頭來看小慧與袁承志。
小慧並沒受傷，只是嚇得怔怔的傻了一般，隔了一會，才撲在母親懷裏哭了出來。袁承志卻滿臉滿身都是鮮血。安大娘忙給他洗抹乾淨，取出刀傷藥給他裹好，幸而兩處刀傷口子都不深，流血雖多，並無大碍。安大娘把他抱到床上睡了，小慧才一五一十地把他剛才捨命相救的情形說了。
安大娘望著袁承志，心想：「瞧不出他小小年紀，居然如此俠義心腸。咱們在這裏是不能躭了，倒要好好成全他一番。」對小慧道：「你也去睡，今天晚上咱們就得走。」
小慧隨著她母親東遷西搬慣了的，也不以為奇。安大娘收拾了一下隨身物件，打了兩個包裹。三人吃過晚飯後，秉燭而坐。她並不閂門，似乎另有所待。
袁承志見她秀眉緊蹙，支頤出神，一會兒眼眶紅了，便似要掉下淚來，心想：「那胡老三說，安嬸嬸的丈夫派他來接小慧回去，不知為了甚麼。她丈夫欺侮安嬸嬸，等我長大了，練好了武藝，定要打她丈夫一頓，給安嬸嬸出氣。只是小慧見我打她爹爹，不知會不會不高興。」又想：「那胡老三說他是錦衣衞的，哼，錦衣衞的人壞死了，我媽媽便是給他們捉去害死的。終有一天，我要大殺錦衣衞的人，給媽媽報仇。」
袁崇煥被崇禎處死後，兄弟妻子都被皇帝下旨充軍三千里。錦衣衞到袁家拿人，袁崇煥的舊部先已得訊，趕去將袁承志救了出來，袁夫人卻未能救出。當年錦衣衞抄家拿人、如虎似狼的兇狠模樣，已深印在袁承志小小的腦海之中。
二更時分，門外輕輕傳來一陣腳步聲，一人飄然進來，原來便是那個啞巴。他身材魁梧壯實，行路卻輕飄飄的，落地僅有微聲。
袁承志見到啞巴，心中大喜，撲上去拉住了他，連問：「崔叔叔呢？他好麼？」竟忘了他是啞的。啞巴裂開了嘴只是傻笑，顯然再見到袁承志也很高與，過了一會，才向安大娘指手劃腳的作了一陣手勢。
安大娘向袁承志道：「崔叔叔沒事，你放心。」和啞巴打了一陣手勢，啞巴不住點頭，雙手連連鼓掌，拍拍聲響。袁承志卻不知他對甚麼事如此衷心贊成。
